<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">사용자 관리</h2>
      <p class="content-sub-title">사용자의 정보를 관리하는 페이지</p>

      <!-- 검색 컨트롤 -->
      <div class="d-flex justify-content-end name-group mt-2">
        <label for="nameSearch" class="mr-2">이름 :</label>
        <input type="text" class="form-control w-auto" id="nameSearch" v-model="nameSearch" placeholder="이름 입력" />
        <button class="btn btn-primary ml-2" @click="search">조회</button>
      </div>

      <!-- 테이블 보기 -->
      <table class="table mt-3" id="dataTable">
        <thead>
          <tr>
            <th>이름</th>
            <th class="d-none d-md-table-cell">이메일</th>
            <th class="d-none d-md-table-cell">부서</th>
            <th>팀</th>
            <th>직책</th>
            <th>상태</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in paginatedData" :key="index" @click="showModal(item)">
            <td>{{ item.name }}</td>
            <td class="d-none d-md-table-cell">{{ item.email }}</td>
            <td class="d-none d-md-table-cell">{{ item.department }}</td>
            <td>{{ item.team }}</td>
            <td>{{ item.position }}</td>
            <td>{{ item.status }}</td>
          </tr>
        </tbody>
      </table>

      <!-- 페이징 컨트롤 -->
      <nav class="d-flex justify-content-center">
        <ul class="pagination">
          <li class="page-item" :class="{ disabled: currentPage === 1 }" @click.prevent="prevPage">
            <a class="page-link" href="#">&lt;</a>
          </li>
          <li class="page-item"><a class="page-link" href="#">{{ currentPage }}</a></li>
          <li class="page-item" :class="{ disabled: currentPage === totalPages }" @click.prevent="nextPage">
            <a class="page-link" href="#">&gt;</a>
          </li>
        </ul>
      </nav>

      <!-- 사용자 세부 정보 모달 창 -->
      <b-modal v-model="modalVisible" title="사용자 세부 정보" @ok="handleSaveClick" ok-title="저장" cancel-title="취소"
        no-close-on-backdrop>
        <b-form @change="handleChange">
          <!-- 이름, 생년월일 한 줄 -->
          <b-form-group label="이름/생년월일" label-for="userDetails" class="user-detail-group">
            <div id="userDetails" class="d-flex">
              <b-form-input v-model="selectedUser.name" :disabled="true" placeholder="이름" class="mr-2"></b-form-input>
              <b-form-input id="userBirthDate" type="date" v-model="selectedUser.birthDate"
                :disabled="true"></b-form-input>
            </div>
          </b-form-group>

          <!-- 사용자 비밀번호 강제 초기화 -->
          <b-form-group label="비밀번호/비밀번호 확인" label-for="userPassword" class="user-detail-group">
            <div id="userPassword" class="d-flex">
              <b-form-input v-model="selectedUser.password" placeholder="비밀번호" class="mr-2"></b-form-input>
              <b-form-input v-model="passwordCheck" placeholder="비밀번호 확인" class="mr-2"></b-form-input>
            </div>
            <!-- 비밀번호 불일치 경고 메시지 -->
            <p v-if="isPasswordMismatch" class="userPasswordNotMatch">비밀번호와 재확인 값이 일치하지 않습니다.</p>
          </b-form-group>


          <!-- 고용형태, 입사일/퇴사일 -->
          <b-form-group label="고용형태/입사일/퇴사일" label-for="employmentDetails" class="user-detail-group">
            <div id="employmentDetails" class="d-flex">
              <b-form-select v-model="selectedUser.employmentType" :options="['정규직', '프리랜서']" :disabled="!isAdmin"
                class="mr-2"></b-form-select>
              <b-form-select v-model="selectedUser.status" :options="['입사', '퇴사']" class="mr-2"></b-form-select>
              <b-form-input type="date" v-model="selectedUserDate" :disabled="true"></b-form-input>
            </div>
          </b-form-group>

          <!-- 부서, 팀, 직책 한 줄 -->
          <b-form-group label="부서/팀/직책" label-for="userEmploymentDetails" class="user-detail-group">
            <div id="userEmploymentDetails" class="d-flex">
              <b-form-input v-model="selectedUser.department" :disabled="!isAdmin" placeholder="부서"
                class="mr-2"></b-form-input>
              <b-form-input v-model="selectedUser.team" :disabled="!isAdmin" placeholder="팀"
                class="mr-2"></b-form-input>
              <b-form-input v-model="selectedUser.position" :disabled="!isAdmin" placeholder="직책"></b-form-input>
            </div>
          </b-form-group>

          <!-- 우편번호 및 주소 -->
          <b-form-group label="우편번호/주소" label-for="userZipCode" class="user-detail-group">
            <div id="userAddress" class="d-flex">
              <b-form-input v-model="selectedUser.zipCode" :disabled="true" placeholder="우편번호" class="mr-2"
                style="width: 33%"></b-form-input>
            </div>
            <b-form-input v-model="selectedUser.address" :disabled="true" placeholder="주소"
              style="width: 100%"></b-form-input>
          </b-form-group>

          <!-- 현재 파견지 -->
          <b-form-group label="현재 파견지" label-for="userLocation" class="user-detail-group">
            <b-form-input id="userLocation" v-model="selectedUser.location" :disabled="!isAdmin"
              placeholder="현재 파견지"></b-form-input>
          </b-form-group>
        </b-form>
      </b-modal>

      <!-- CustomModal 컴포넌트 사용: 저장 확인용 모달 -->
      <AlertModal 
        :isVisible="confirmationModalVisible" 
        :disableBackgroundClose="true"
        title="저장 확인" 
        confirmText="확인" 
        cancelText="취소"
        @close="confirmationModalVisible = false" 
        @confirm="saveUserDetails">
        <template #body>
          <p>변경 사항을 저장하시겠습니까?</p>
          <p v-if="statusChanged" style="color: red;">입사/퇴사 처리가 포함되어 있습니다.</p>
        </template>
      </AlertModal>

      <!-- CustomModal 컴포넌트 사용: 변경된 데이터가 없을 때 표시할 모달 -->
      <AlertModal 
        :isVisible="noChangesModalVisible" 
        title="알림" 
        confirmText="확인" 
        @close="closeNoChangesModal"
        @confirm="closeNoChangesModal">
        <template #body>
          <p>변경된 데이터가 없습니다.</p>
        </template>
      </AlertModal>

      <!-- CustomModal 컴포넌트 사용: 저장 완료 모달 -->
      <AlertModal 
        :isVisible="saveCompleteModalVisible" 
        :title="saveCompleteMessageTitle" 
        confirmText="확인"
        @close="closeSaveCompleteModal" 
        @confirm="closeSaveCompleteModal">
        <template #body>
          <p>{{ saveCompleteMessage }}</p>
        </template>
      </AlertModal>

    </div>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue';
import axios from 'axios';
import AlertModal from '@/components/modal/AlertModal.vue'; // 새로운 컴포넌트 import

// 관리자 여부
const isAdmin = ref(true);

// 모달 관련 상태
const modalVisible = ref(false);
const confirmationModalVisible = ref(false);
const saveCompleteModalVisible = ref(false);
const noChangesModalVisible = ref(false); // 변경된 데이터가 없을 때 사용할 모달
const saveCompleteMessageTitle = ref('저장 완료');
const saveCompleteMessage = ref('저장되었습니다.');  // 저장 완료 모달 메시지

const selectedUser = ref({
  name: '',
  email: '',
  password: '',
  department: '',
  team: '',
  position: '',
  joinDate: '',
  leaveDate: '',
  status: '입사',
  birthDate: '',
  employmentType: '정규직',
  zipCode: '',
  address: '',
  location: ''
});
const originalUser = ref({ ...selectedUser.value });

// 상태 변경 감지 변수
const statusChanged = ref(false);
const dataChanged = ref(false);
// 사용자 체크 패스워드
const passwordCheck = ref('');
// 비밀번호 불일치 상태
const isPasswordMismatch = ref(false);

// 변경 사항 감지 함수
const handleChange = () => {
  isPasswordMismatch.value = selectedUser.value.password !== passwordCheck.value;
  dataChanged.value = JSON.stringify(originalUser.value) !== JSON.stringify(selectedUser.value);
  statusChanged.value = selectedUser.value.status !== originalUser.value.status;
};

// 사용자 클릭 시 모달 열기
const showModal = (user) => {
  selectedUser.value = { ...user };
  originalUser.value = { ...user }; // 기존 값 저장
  passwordCheck.value = user.password;
  statusChanged.value = false;
  dataChanged.value = false;
  isPasswordMismatch.value = false;
  modalVisible.value = true;
};

// 저장 버튼을 클릭했을 때 호출되는 함수
const handleSaveClick = () => {
  if (isPasswordMismatch.value) {
    saveCompleteMessageTitle.value = '비밀번호 불일치';
    saveCompleteMessage.value = '비밀번호와 재확인의 값이 일치하지 않습니다.';
    saveCompleteModalVisible.value = true;
    return;
  }
  if (!dataChanged.value) {
    noChangesModalVisible.value = true; // 변경된 데이터가 없을 때 모달 띄우기
  } else {
    showConfirmationModal(); // 데이터가 변경되었을 때 저장 확인 모달 띄우기
  }
};

// 모달 닫기 함수
const closeNoChangesModal = () => {
  noChangesModalVisible.value = false;
};

// 저장 확인 모달 열기 함수
const showConfirmationModal = () => {
  confirmationModalVisible.value = true; // 재확인 모달 열기
};

// 저장 완료 모달 닫기 함수
const closeSaveCompleteModal = () => {
  saveCompleteModalVisible.value = false;
};

// 저장 함수 (재확인 모달에서 확인 버튼 클릭 시 호출)
const saveUserDetails = () => {

  console.log("saveUserDetails >>>>>>>>>> ");

  modalVisible.value = false; // 기존 모달 닫기
  confirmationModalVisible.value = false; // 저장 확인 모달 닫기

  // Axios를 이용해 백엔드에 저장 요청
  axios.post('/api/saveUser', selectedUser.value)
    .then(response => {
      console.log(response.data);
      // 성공 시 저장 완료 모달 띄우기
      saveCompleteMessageTitle.value = '저장 완료';
      saveCompleteMessage.value = '저장되었습니다.';
      saveCompleteModalVisible.value = true;
    })
    .catch(error => {
      console.log(error);
      // 실패 시 에러 메시지 출력
      saveCompleteMessageTitle.value = '저장 실패';
      saveCompleteMessage.value = '저장에 실패했습니다.(기능 미구현)';
      saveCompleteModalVisible.value = true;
    });
};

// 사용자 데이터 및 페이징 처리
const currentPage = ref(1);
const rowsPerPage = ref(5);
const nameSearch = ref('');
const data = ref([
  { name: '권범준', email: 'qjawns0618@kpcnc.co.kr', password: '7777', department: 'SI사업본부', team: '프레임워크', position: '책임', joinDate: '2022-01-01', leaveDate: '', status: '입사', birthDate: '1990-01-01', employmentType: '정규직', zipCode: '12345', address: '서울 강남구', location: '서울' },
  { name: '김철수', email: 'kim@example.com', password: '1237', department: 'AI사업본부', team: 'RPA', position: '책임', joinDate: '2021-07-15', leaveDate: '', status: '퇴사', BirthDate: '1985-06-15', employmentType: '프리랜서', zipCode: '67890', address: '부산 해운대구', location: '부산' },
  { name: '이영희', email: 'lee@example.com', password: '1234', department: '경영관리부', team: '영업', position: '선임', joinDate: '2023-03-12', leaveDate: '', status: '입사', BirthDate: '1995-03-12', employmentType: '정규직', zipCode: '54321', address: '인천 서구', location: '인천' },
]);

const filteredData = computed(() => {
  return data.value.filter(item => item.name.includes(nameSearch.value));
});

const paginatedData = computed(() => {
  const start = (currentPage.value - 1) * rowsPerPage.value;
  return filteredData.value.slice(start, start + rowsPerPage.value);
});

const totalPages = computed(() => Math.ceil(filteredData.value.length / rowsPerPage.value));

// 사용자 검색
const search = () => {
  currentPage.value = 1;
};

// 페이지 변경
const prevPage = () => {
  if (currentPage.value > 1) currentPage.value--;
};
const nextPage = () => {
  if (currentPage.value < totalPages.value) currentPage.value++;
};

// 입사/퇴사 날짜 계산
const selectedUserDate = computed(() => {
  return selectedUser.value.status === '입사' ? selectedUser.value.joinDate : selectedUser.value.leaveDate;
});

</script>


<style scoped>
/* 모달 전체 배경 */
.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  /* 가로 전체 크기 */
  height: 100vh;
  /* 세로 전체 크기 */
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1050;
}

/* 모달 다이얼로그 */
.modal-dialog {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 400px;
  /* 크기 더 작게 조정 */
  margin: 0 auto;
}

/* 모달 내용 */
.modal-content {
  background-color: #fff;
  border-radius: 8px;
  padding: 20px;
  width: 100%;
  max-width: 400px;
  /* 크기 더 작게 조정 */
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
}

/* 모달 헤더: 왼쪽 정렬 */
.modal-header h5 {
  font-size: 1rem;
  /* 기본 타이틀 크기 조정 */
  text-align: left;
  margin-bottom: 8px;
}

/* 모달 바디: 왼쪽 정렬 */
.modal-body {
  padding: 10px 0;
  text-align: left;
  font-size: 0.9rem;
  /* 기본 바디 크기 조정 */
}

/* 모달 푸터: 오른쪽 정렬 */
.modal-footer {
  display: flex;
  justify-content: flex-end;
  border-top: 1px solid #dee2e6;
  padding-top: 10px;
  margin-top: 10px;
}

/* 모달 버튼 스타일 */
.modal-footer .btn {
  min-width: 80px;
  margin-left: 10px;
}

/* 모달 헤더: 실선 제거 */
.modal-header {
  border-bottom: none;
}

/* 모달 푸터: 실선 제거 */
.modal-footer {
  border-top: none;
  padding: 0;
  margin: 0;
}

.modal-header,
.modal-footer {
  padding: 0;
}

.userPasswordNotMatch {
  color: red;
}

.user-detail-group {
  margin-bottom: 10px !important;
}

#userAddress {
  margin-bottom: 10px;
}

/* 반응형: 모바일 환경에서 폰트 크기 조정 */
@media (max-width: 768px) {

  .modal-header h5 {
    font-size: 0.8rem;
    /* 모바일에서 타이틀 크기 조정 */
  }

  .modal-body {
    font-size: 0.75rem;
    /* 모바일에서 바디 크기 조정 */
  }
}

/* 모바일 화면에서 모달 크기 조정 */
@media (max-width: 576px) {

  .modal-content {
    max-width: 80%;
    /* 모바일에서 전체 크기 줄이기 */
    padding: 15px;
    /* 모바일에서 패딩 조정 */
  }
}
</style>
