<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">결제 마감 확인</h2>
      <p class="content-sub-title">결제 마감 후 정상적 처리 확인 페이지</p>

      <div class="search-controls">
        <!-- 월 선택 그룹 (오른쪽 정렬) -->
        <div class="d-flex align-items-center end-date-group mt-2 justify-content-end">
          <label for="endDate" class="mr-2">월 선택 :</label>
          <input type="month" class="form-control w-auto" id="endDate" v-model="endDate">
        </div>
        <!-- 이름 검색 그룹 (실시간 필터링) -->
        <div class="d-flex align-items-center name-group mt-2 justify-content-end">
          <label for="nameSearch" class="mr-2">이름 :</label>
          <input
            type="text"
            class="form-control w-auto"
            id="nameSearch"
            v-model="nameSearch"
            placeholder="이름 입력"
            @input="search"
          />
        </div>
      </div>

      <!-- 테이블 보기 -->
      <table class="table mt-3" id="dataTable" v-if="viewMode === 'table'">
        <thead>
          <tr>
            <th>이름</th>
            <th>부서</th>
            <th>팀</th>
            <th>건수</th>
            <th>승인금액</th>
            <th>반려금액</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in paginatedData" :key="index">
            <td>{{ item.name }}</td>
            <td>{{ item.department }}</td>
            <td>{{ item.team }}</td>
            <td>{{ item.count }}</td>
            <td>{{ item.approved.toLocaleString() }}원</td>
            <td>{{ item.rejected.toLocaleString() }}원</td>
          </tr>
        </tbody>
      </table>

      <!-- 카드 형식 보기 -->
      <div class="card mb-3" v-for="(item, index) in paginatedData" :key="index">
        <div class="card-header">
          <div class="d-flex justify-content-between align-items-center mb-2 card-header-body">
            <p class="card-text m-0">{{ item.name }}</p>
          </div>
        </div>
        <div class="card-body">
          <p class="card-text"><Strong>건수: </Strong>{{ item.count }}</p>
          <p class="card-text"><Strong>부서: </Strong>{{ item.department }}</p>
          <p class="card-text"><Strong>팀: </Strong>{{ item.team }}</p>
          <p class="card-text"><Strong>승인금액: </Strong>{{ item.approved.toLocaleString() }}원</p>
          <p class="card-text"><Strong>반려금액: </Strong>{{ item.rejected.toLocaleString() }}원</p>
        </div>
      </div>

      <!-- 페이징 컨트롤 -->
      <nav class="d-flex justify-content-center">
        <ul class="pagination">
          <li class="page-item" :class="{ disabled: currentPage === 1 }" @click.prevent="prevPage"><a class="page-link" href="#">&lt;</a></li>
          
          <!-- 1-5 페이지 범위 표시 -->
          <li v-for="page in pageNumbers" :key="page" class="page-item" :class="{ active: currentPage === page }">
            <a class="page-link" href="#" @click.prevent="goToPage(page)">{{ page }}</a>
          </li>

          <li class="page-item" :class="{ disabled: currentPage === totalPages }" @click.prevent="nextPage"><a class="page-link" href="#">&gt;</a></li>
        </ul>
      </nav>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';

// 종료월 선택과 이름 검색 관련 변수
const endDate = ref('');
const nameSearch = ref('');
const currentPage = ref(1);
const rowsPerPage = ref(5);
const viewMode = ref('table');  // 테이블 또는 카드 형식
const data = ref([]);
const filteredData = ref([]);

// 현재 날짜에서 월 값을 가져오기 위한 함수
const getCurrentMonth = () => {
  const today = new Date();
  return today.toISOString().slice(0, 7); // "YYYY-MM" 형식 반환
};

// 예시 데이터 설정
const fetchData = () => {
  data.value = [
    { name: '김필수', department: 'SI사업본부', team: '프레임워크', count: 3, approved: 800000, rejected: 200000 },
    { name: '홍길동', department: 'SI사업본부', team: '프레임워크', count: 3, approved: 800000, rejected: 200000 },
    { name: '김한', department: 'SI사업본부', team: '프레임워크', count: 5, approved: 1000000, rejected: 200000 },
    { name: '이영희', department: 'SI사업본부', team: '프레임워크', count: 2, approved: 500000, rejected: 300000 },
    { name: '이영희', department: 'SI사업본부', team: '프레임워크', count: 5, approved: 1182209, rejected: 179964 },
    { name: '박지민', department: 'SI사업본부', team: '프레임워크', count: 1, approved: 1029921, rejected: 334995 },
    { name: '김철수', department: 'SI사업본부', team: '프레임워크', count: 2, approved: 971966, rejected: 450184 },
    { name: '최수진', department: 'SI사업본부', team: '프레임워크', count: 3, approved: 1154275, rejected: 381772 },
    { name: '장미래', department: 'SI사업본부', team: '프레임워크', count: 3, approved: 578181, rejected: 127548 },
    { name: '홍길동', department: 'SI사업본부', team: '프레임워크', count: 1, approved: 931208, rejected: 262131 },
    { name: '장미래', department: 'SI사업본부', team: '프레임워크', count: 4, approved: 1182861, rejected: 438678 },
    { name: '강민호', department: 'SI사업본부', team: '프레임워크', count: 3, approved: 611794, rejected: 339043 },
    { name: '최수진', department: 'SI사업본부', team: '프레임워크', count: 1, approved: 1245448, rejected: 295608 },
    { name: '신동엽', department: 'SI사업본부', team: '프레임워크', count: 1, approved: 1285861, rejected: 431430 }
  ];
  filteredData.value = data.value;
};

// 페이지네이션 계산
const paginatedData = computed(() => {
  const start = (currentPage.value - 1) * rowsPerPage.value;
  return filteredData.value.slice(start, start + rowsPerPage.value);
});

const totalPages = computed(() => {
  return Math.ceil(filteredData.value.length / rowsPerPage.value);
});

// 1-5 페이지 범위 표시
const pageNumbers = computed(() => {
  const maxPages = 5;
  const startPage = Math.max(1, currentPage.value - Math.floor(maxPages / 2));
  const endPage = Math.min(totalPages.value, startPage + maxPages - 1);
  const pages = [];
  for (let i = startPage; i <= endPage; i++) {
    pages.push(i);
  }
  return pages;
});

// 이름 검색 (실시간 필터링)
const search = () => {
  const searchName = nameSearch.value.toLowerCase();
  filteredData.value = data.value.filter(item => item.name.toLowerCase().includes(searchName));
  currentPage.value = 1;
};

// 특정 페이지로 이동
const goToPage = (page) => {
  currentPage.value = page;
};

// 이전 페이지
const prevPage = () => {
  if (currentPage.value > 1) {
    currentPage.value--;
  }
};

// 다음 페이지
const nextPage = () => {
  if (currentPage.value < totalPages.value) {
    currentPage.value++;
  }
};

onMounted(() => {
  endDate.value = getCurrentMonth(); // 페이지 로드 시 현재 달로 설정
  fetchData();
});
</script>

<style scoped>
#dataTable tbody tr:hover td {
  background-color: #e7f1ff !important;
}
@media (min-width: 1151px) {
  .search-controls {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
  }
  .search-controls > div {
    margin-right: 1rem;
    margin-bottom: 0;
  }
  #nameSearch {
    font-size: 1rem;
  }
  .form-control,
  input[type="month"],
  #searchButton {
    font-size: 1rem;
    padding: 0.375rem 0.75rem;
    width: auto;
  }
  .card {
    display: none; /* 카드 숨기기 */
  }
  .table {
    display: table; /* 테이블 표시 */
  }
}

@media (max-width: 1150px) {
  .card {
    width: 100%;
    border: 1px solid #ddd;
    padding: 10px;
    border-radius: 5px;
  }
  .btn-primary {
    font-size: 0.75rem; /* 모바일 버튼 글자 크기 줄이기 */
    padding: 0.3rem 0.6rem; /* 모바일 버튼 패딩 줄이기 */
    margin-bottom: 10px;
  }
  .search-controls {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .search-controls > div {
    width: 100%;
    display: flex;
    justify-content: flex-end;
  }
  .form-control,
  input[type="month"] {
    width: 150px;
    font-size: 0.8rem;
    padding: 0.25rem 0.5rem;
  }
  #nameSearch {
    width: 150px;
    font-size: 0.8rem;
    padding: 0.25rem 0.5rem;
    margin-bottom: 7px;
  }
  #searchButton {
    font-size: 0.8rem;
    padding: 0.25rem 0.5rem;
  }
  .table {
    display: none; /* 테이블 숨기기 */
  }
}
@media (max-width: 768px) {
  .search-controls {
    display: flex;
    flex-direction: column;
    align-items: flex-end; /* 오른쪽 정렬 */
  }
  .end-date-group, .name-group {
    width: 100%;
    display: flex;
    justify-content: flex-end; /* 오른쪽 정렬 */
  }
  .btn-primary {
    font-size: 0.75rem; /* 모바일 버튼 글자 크기 줄이기 */
    padding: 0.3rem 0.6rem; /* 모바일 버튼 패딩 줄이기 */
    margin-bottom: 10px;
  }
  .form-control,
  input[type="month"] {
    width: 150px;
    font-size: 0.8rem;
    padding: 0.25rem 0.5rem;
  }
  #nameSearch {
    width: 150px;
    font-size: 0.8rem;
    padding: 0.25rem 0.5rem;
    margin-bottom: 7px;
  }
  .card-header-body {
    margin-bottom: 0 !important;
  }
}
</style>
