<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content" :class="{ 'content-expanded': sidebarVisible && !isMobile }">
      <h2 class="content-title">권범준님 영수증 신청내역</h2>
      <div class="search-date-range mt-3 search-month-day">
        <p>날짜: [{{ filteredStartDate }} ~ {{ filteredEndDate }}]</p>
      </div>
      <!-- 뒤로가기 버튼 -->
      <button class="btn btn-secondary back-button" @click="goBack">뒤로가기</button>

      <div class="d-flex align-items-center">
        <span class="mr-2 all-click">전체 승인</span>
        <input type="radio" name="approveAll" id="approveAll" @change="toggleApproveAll" v-model="selectAllDecision"
          value="approve" class="mr-4" />
        <span class="mr-2 all-click">전체 반려</span>
        <input type="radio" name="rejectAll" id="rejectAll" @change="toggleRejectAll" v-model="selectAllDecision"
          value="reject" />
      </div>
      <hr>

      <!-- 테이블 보기 (큰 화면에서만 보임) -->
      <table class="table mt-3" id="dataTable" v-if="!isMobile">
        <thead>
          <tr>
            <th>발생일</th>
            <th>인원</th>
            <th>구분</th>
            <th>사유</th>
            <th>금액</th>
            <th>영수증 사진</th>
            <th>승인</th>
            <th>반려</th>
            <th>반려사유</th>
          </tr>
        </thead>
        <tbody>
          <template v-for="item in paginatedData" :key="item.date">
            <tr @click="openSidebar(item)">
              <td>{{ item.date }}</td>
              <td>{{ item.people.length }}</td>
              <td>{{ item.type }}</td>
              <td>{{ item.reason }}</td>
              <td>{{ item.amount.toLocaleString() }}원</td>
              <td><a href="#" @click.prevent="openPreviewModal(item.receipt)" class="text-primary">{{ item.receiptName
                  }}</a></td>
              <td><input type="radio" :name="'decision' + index" class="approveRadio" v-model="item.decision"
                  value="approve" @change="updateDecision()" /></td>
              <td><input type="radio" :name="'decision' + index" class="rejectRadio" v-model="item.decision"
                  value="reject" @change="updateDecision()" /></td>
              <td><input type="text" class="form-control rejectReason" v-model="item.rejectReason" /></td>
            </tr>
          </template>
        </tbody>
      </table>

      <!-- 카드 레이아웃 보기 (작은 화면에서만 보임) -->
      <div class="card-layout" v-if="isMobile">
        <div class="card mb-3" v-for="(item, index) in paginatedData" :key="index">
          <div class="card-header">
            <p class="card-title">{{ item.date }}</p>
          </div>
          <div class="card-body">
            <p class="card-text"><strong>인원 : </strong>
              <span @click="togglePeopleList(item)" style="cursor: pointer;">{{ item.people.length }}명</span>
            </p>
            <div v-if="showPeopleList(item)">
              <div class="people-list">
                <div class="people-list-header">명단</div>
                <p v-for="person in item.people" :key="person.name" class="people-list-item">{{ person.name }} ({{
                  person.department }} - {{ person.team }})</p>
              </div>
              <hr />
            </div>
            <p class="card-text"><strong>구분/사유 : </strong> {{ item.type }} / {{ item.reason }}</p>
            <p class="card-text"><strong>금액 : </strong> {{ item.amount }}</p>
            <p class="card-text"><strong>금액/인원수 : </strong> {{ calculateAmountPerPerson(item) }}</p>
            <p>
              <strong class="card-text">영수증 사진 : </strong>
              <a class="card-text" @click.prevent="openPreviewModal(item.receipt)"
                style="cursor: pointer; color: blue;">{{
                  item.receiptName }}</a>
            </p>
            <p class="card-text">
              <strong>결정: </strong>
              승인 <input type="radio" :name="'decision' + index" class="approveRadio" v-model="item.decision"
                value="approve" @change="updateDecision()" /> |
              반려 <input type="radio" :name="'decision' + index" class="rejectRadio" v-model="item.decision"
                value="reject" @change="updateDecision()" /><br>
            </p>
            <p class="card-text d-flex align-items-center justify-content-between">
              <strong>반려사유: </strong>
              <input type="text" class="form-control rejectReason ml-2" v-model="item.rejectReason" style="flex: 1;" />
            </p>
          </div>
        </div>
      </div>

      <!-- 저장 버튼 -->
      <div class="d-flex justify-content-end align-items-center mb-3">
        <button class="btn btn-primary ml-3" id="saveButton" @click="save">저장</button>
      </div>

      <!-- 페이징 -->
      <div class="d-flex justify-content-center">
        <ul class="pagination">
          <li class="page-item" @click.prevent="prevPage"><a class="page-link" href="#" id="prevPage">&lt;</a></li>
          <li class="page-item"><a class="page-link" href="#" id="currentPage">{{ currentPage }}</a></li>
          <li class="page-item" @click.prevent="nextPage"><a class="page-link" href="#" id="nextPage">&gt;</a></li>
        </ul>
      </div>

      <!-- 사이드바 추가 -->
      <div :class="['sidebar-wrapper', { 'sidebar-visible': sidebarVisible }]">
        <ReceiptSidebar 
          v-if="!isMobile" 
          :item="selectedItem" 
          :visible="sidebarVisible" 
          @close="closeSidebar"
          @preview-image="openPreviewModal" />
      </div>
      
      <!-- 이미지 미리보기 모달 -->
      <div v-if="isPreviewVisible" class="modal preview-modal" @click="closePreviewModalOnOutsideClick">
        <div
          class="preview-modal-content"
          @mousedown="startDrag"
          @mousemove="onDrag"
          @mouseup="endDrag"
          @mouseleave="endDrag"
        >
          <img
            :src="previewImage"
            :class="{ zoomed: isZoomed }"
            class="preview-modal-image"
            :style="{
              transform: isZoomed
                ? `translate(${zoomedPosition.x}px, ${zoomedPosition.y}px) scale(1.5)`
                : 'none',
              transformOrigin: `${zoomOrigin.x}px ${zoomOrigin.y}px`
            }"
            @dblclick="toggleZoom"
          />
        </div>
      </div>


    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
// import * as bootstrap from 'bootstrap'; // Bootstrap 모듈 가져오기
import { useRouter } from 'vue-router'; // vue-router import
import ReceiptSidebar from './ReceiptSidebar.vue'; // 사이드바 컴포넌트 임포트

const selectedItem = ref(null);  // 사이드바에 표시할 선택된 항목
const sidebarVisible = ref(false);  // 사이드바 가시성 제어
const isMobile = ref(window.innerWidth <= 1400);
const currentPage = ref(1);
const rowsPerPage = ref(5);
const selectAllDecision = ref(''); // 전체 승인/반려를 선택하는 라디오 버튼의 v-model
const filteredStartDate = ref('2024-01');
const filteredEndDate = ref('2024-03');
const visiblePeopleLists = ref({});
import { usePreviewModal } from '@/utils/preview-modal';

const {
  isPreviewVisible,
  previewImage,
  isZoomed,
  zoomedPosition,
  zoomOrigin,
  openPreviewModal,
  toggleZoom,
  startDrag,
  onDrag,
  endDrag
} = usePreviewModal();

const data = ref([
  {
    date: '2024-08-01',
    type: '식비',
    reason: '야근',
    amount: '100,000',
    receiptName: '영수증 예시',
    receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
    //receipt: '/img/receipt_example.jpg',
    rejectionReason: '',
    people: [
      { department: 'SI사업본부', team: '프레임워크', name: '권범준', position: '책임' },
      { department: 'SI사업본부', team: '프레임워크', name: '홍길동', position: '선임' },
    ],
  },
  {
    date: '2024-08-02',
    type: '식비',
    reason: '야근',
    amount: '110,000',
    receiptName: '영수증 예시',
    receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
    //receipt: '/img/receipt_example.jpg',
    rejectionReason: '',
    people: [
      { department: 'SI사업본부', team: '프레임워크', name: '권범준', position: '책임' },
      { department: 'SI사업본부', team: '프레임워크', name: '홍길동', position: '선임' },
      { department: 'SI사업본부', team: '프레임워크', name: '심청이', position: '선임' },
      { department: 'SI사업본부', team: '프레임워크', name: '똑순이', position: '선임' },
      { department: 'SI사업본부', team: '프레임워크', name: '복순이', position: '선임' },
      { department: 'SI사업본부', team: '프레임워크', name: '황순이', position: '선임' },
    ],
  },
  // 추가 데이터...
]);

// Vue 이전 페이지
const router = useRouter();
const goBack = () => {
  router.back(); // 뒤로가기
};

const paginatedData = computed(() => {
  const start = (currentPage.value - 1) * rowsPerPage.value;
  return data.value.slice(start, start + rowsPerPage.value);
});

const save = () => {
  console.log('Data saved:', data.value);
};

const toggleApproveAll = () => {
  if (selectAllDecision.value === 'approve') {
    data.value.forEach(item => item.decision = 'approve');
  } else {
    data.value.forEach(item => item.decision = null);
  }
};

const toggleRejectAll = () => {
  if (selectAllDecision.value === 'reject') {
    data.value.forEach(item => item.decision = 'reject');
  } else {
    data.value.forEach(item => item.decision = null);
  }
};

const updateDecision = () => {
  const allApproved = data.value.every(item => item.decision === 'approve');
  const allRejected = data.value.every(item => item.decision === 'reject');

  if (allApproved) {
    selectAllDecision.value = 'approve';
  } else if (allRejected) {
    selectAllDecision.value = 'reject';
  } else {
    selectAllDecision.value = '';
  }
};

const prevPage = () => {
  if (currentPage.value > 1) {
    currentPage.value--;
  }
};

const nextPage = () => {
  const totalPages = Math.ceil(data.value.length / rowsPerPage.value);
  if (currentPage.value < totalPages) {
    currentPage.value++;
  }
};

const openSidebar = (item) => {
  if (!isMobile.value) {
    selectedItem.value = item;
    sidebarVisible.value = true;
  }
};

const closeSidebar = () => {
  sidebarVisible.value = false;
};

const closePreviewModalOnOutsideClick = (event) => {
  if (!event.target.classList.contains('preview-modal-image')) {
    isPreviewVisible.value = false;
  }
};

const updateIsMobile = () => {
  isMobile.value = window.innerWidth <= 1400;
};

// 금액/인원수 계산
const calculateAmountPerPerson = (item) => {
  const amount = parseInt(item.amount.replace(/[^0-9]/g, ''), 10); // 금액에서 숫자만 추출
  const peopleCount = item.people.length;
  return peopleCount > 0 ? Math.floor(amount / peopleCount).toLocaleString() + ' 원' : 'N/A';
};

const togglePeopleList = (item) => {
  if (visiblePeopleLists.value[item.date]) {
    delete visiblePeopleLists.value[item.date];
  } else {
    visiblePeopleLists.value[item.date] = true;
  }
};

const showPeopleList = (item) => {
  return visiblePeopleLists.value[item.date];
};


onMounted(() => {
  window.addEventListener('resize', updateIsMobile);
});
</script>

<style scoped>
#dataTable tbody tr:hover td {
  background-color: #e7f1ff !important;
}

.content {
  transition: margin-right 0.3s ease;
}

.content-expanded {
  margin-right: 300px;
  /* 사이드바가 나왔을 때 테이블이 밀리는 효과 */
}

.btn {
  padding: 0.5rem 1rem;
}

.back-button {
  padding: 0.3rem 0.6rem !important;
  font-size: 0.7rem;
  margin-bottom: 60px;
}

.text-primary {
  color: #c7573e;
  /* 원하는 하이퍼링크 색상으로 변경 */
  text-decoration: none;
  /* 하이퍼링크 밑줄 제거 */
}

.d-flex.align-items-center {
  align-items: center;
}

/* 사이드바 감싸는 wrapper 스타일 및 애니메이션 */
.sidebar-wrapper {
  position: fixed;
  right: 0;
  top: 0;
  width: 300px;
  height: 100%;
  transform: translateX(100%);
  /* 기본 상태: 화면 밖으로 */
  transition: transform 0.3s ease;
  /* 슬라이드 애니메이션 효과 */
  z-index: 1000;
}

/* 사이드바가 보일 때 적용되는 클래스 */
.sidebar-visible {
  transform: translateX(0);
  /* 화면 안으로 슬라이드 */
}

/* 사이드바 내용물 스타일 유지 */
.sidebar {
  width: 100%;
  height: 100%;
  background-color: #f9f9f9;
  border-left: 1px solid #ddd;
  padding: 20px;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
}

.card-layout {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.card {
  width: 100%;
  border: 1px solid #ddd;
  padding: 10px;
  border-radius: 5px;
}

.card-header {
  font-size: 1.2rem;
  font-weight: bold;
}

.all-click {
  font-weight: bold;
}

.search-month-day {
  font-weight: bold;
}

/* 인원 목록 스타일 추가 */
.people-list {
  font-size: 0.7rem;
  max-height: 150px;
  overflow-y: auto;
  margin-top: 10px;
  border-top: 1px solid #ddd;
  padding-top: 10px;
}

.people-list-header {
  font-weight: bold;
  margin-bottom: 5px;
}

.people-list-item {
  padding: 1px 0;
  margin: 0;
  margin-left: 10px;
}

@media (max-width: 1400px) {
  .card {
    margin-bottom: 10px;
  }

  .card-header {
    background-color: #375a7f;
    font-weight: bold;
    /* 글자 진하게 */
    color: #ffffff;
    /* 텍스트 색상 */
  }

  .card-title {
    font-weight: bold;
    font-size: 1rem;
    margin-bottom: 0px;
  }

  .page-link {
    font-size: 0.75rem;
    padding: 0.3rem 0.6rem;
  }

  .page-item {
    font-size: 0.75rem;
  }

  .pagination {
    margin: 3px 0 60px 0;
  }
}

@media (max-width: 768px) {
  .back-button {
    margin-bottom: 40px;
  }

  .rejectReason {
    width: 100%;
  }
}
</style>
