<template>
  <div class="container mt-5">
    <div class="row justify-content-center">
      <div class="col-md-8">
        <div class="card">
          <div class="card-header text-center">
            <img src="https://www.kpcnc.co.kr/src/img/logo.png" class="logo mb-3" alt="logo">
          </div>
          <div class="card-body">
            <form @submit.prevent="handleSubmit">

              <!-- 이름과 핸드폰 번호 -->
              <div class="form-group custom-grid">
                <div>
                  <label for="userName">이름</label>
                  <input type="text" class="form-control" id="userName" v-model="userName" required>
                </div>
                <div>
                  <label for="phone">핸드폰 번호</label>
                  <input type="text" class="form-control" id="phone" v-model="phone" placeholder="'-' 없이 입력하세요" required>
                </div>
              </div>

              <!-- 비밀번호와 비밀번호 확인 -->
              <div class="form-group custom-grid mt-3">
                <div>
                  <label for="password">비밀번호</label>
                  <input type="password" class="form-control" id="password" v-model="password" required>
                </div>
                <div>
                  <label for="confirmPassword">비밀번호 확인</label>
                  <input type="password" class="form-control" id="confirmPassword" v-model="confirmPassword" required>
                </div>
              </div>

              <!-- 생년월일과 입사일 -->
              <div class="form-group custom-grid mt-3">
                <div>
                  <label for="dob">생년월일</label>
                  <input type="date" class="form-control" id="dob" v-model="dob" required>
                </div>
                <div>
                  <label for="joinDate">입사일</label>
                  <input type="date" class="form-control" id="joinDate" v-model="joinDate" required>
                </div>
              </div>

              <!-- 부서, 팀, 직급 -->
              <div class="form-group custom-grid-3 mt-3">
                <div>
                  <label for="department">부서</label>
                  <select class="form-control" id="department" v-model="department" required>
                    <option value="" disabled>부서 선택</option>
                    <option value="IT">IT</option>
                    <option value="HR">HR</option>
                    <option value="Finance">Finance</option>
                  </select>
                </div>
                <div>
                  <label for="team">팀</label>
                  <select class="form-control" id="team" v-model="team" required>
                    <option value="" disabled>팀 선택</option>
                    <option value="Team A">Team A</option>
                    <option value="Team B">Team B</option>
                    <option value="Team C">Team C</option>
                  </select>
                </div>
                <div>
                  <label for="position">직급</label>
                  <input type="text" class="form-control" id="position" v-model="position" placeholder="직급 입력" required>
                </div>
              </div>

              <!-- 우편번호와 주소 -->
              <div class="form-group mt-3">
                <label for="postalCode">우편번호</label>
                <div class="input-group">
                  <input type="text" class="form-control" id="postalCode" v-model="postalCode" placeholder="우편번호" disabled required>
                  <div class="input-group-append">
                    <button class="btn btn-outline-secondary" type="button">주소 검색</button>
                  </div>
                </div>
              </div>

              <div class="form-group mt-3">
                <label for="address">주소</label>
                <input type="text" class="form-control" id="address" v-model="address" placeholder="주소를 입력하세요" disabled required>
              </div>

              <!-- 상세주소 입력 -->
              <div class="form-group mt-3">
                <label for="detailAddress">상세 주소</label>
                <input type="text" class="form-control" id="detailAddress" v-model="detailAddress" placeholder="상세 주소를 입력하세요" required>
              </div>

              <!-- 저장 버튼 -->
              <div class="form-group mt-4 text-center btn-save-back">
                <button type="submit" class="btn btn-primary">가입하기</button>
                <button type="button" class="btn btn-primary btn-back" @click="onGoBack">돌아가기</button>
              </div>

            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue';
import { useRouter } from 'vue-router';

// 라우터 사용 설정
const router = useRouter();

const userName = ref('');
const phone = ref('');
const dob = ref('');
const joinDate = ref('');
const department = ref('');
const team = ref('');  // 팀을 위한 상태 추가
const position = ref('');
const address = ref('');
const postalCode = ref('');
const detailAddress = ref('');
const password = ref('');
const confirmPassword = ref('');

const onGoBack = () => {
  router.push('/');
};

// 폼 제출 시 호출되는 함수
const handleSubmit = () => {
  if (password.value !== confirmPassword.value) {
    alert('비밀번호가 일치하지 않습니다.');
    return;
  }

  // 서버로 데이터를 전송하는 로직을 추가하세요
  console.log('회원가입 완료:', {
    userName: userName.value,
    phone: phone.value,
    dob: dob.value,
    joinDate: joinDate.value,
    department: department.value,
    team: team.value, // 팀 정보 추가
    position: position.value,
    address: address.value,
    detailAddress: detailAddress.value,
    password: password.value
  });
};
</script>

<style scoped>
.container {
  margin-top: 50px;
}

.logo {
  width: 150px;
  margin: 10px 0 10px 0 !important;
}

.card-header {
  background-color: #f8f9fa;
  border-bottom: 1px solid #dee2e6;
}

.btn-primary {
  padding: 10px 30px;
}

.form-group {
  margin-bottom: 20px;
}

.btn-back {
  margin-left: 10px;
  background-color: grey;
}

/* Custom Grid Layout */
.custom-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px; /* 필드 간의 간격 */
}

#postalCode {
  max-width: 30%; /* 우편번호 필드의 너비를 50%로 설정 */
}

/* 모바일에서는 두 개의 필드가 한 줄에 나타나도록 강제 */
@media (max-width: 768px) {
  label {
    font-size: 0.7rem !important;
  }
  .custom-grid {
    grid-template-columns: repeat(2, 1fr); /* 필드를 두 개씩 한 줄에 나타내도록 강제 */
  }
  /* 부서, 팀, 직급을 한 줄에 3개 배치 */
  .custom-grid-3 {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px; /* 필드 간의 간격 */
  }
  .form-group {
    margin-top: 10px !important;
    margin-bottom: 0px !important;
  }
  .btn-save-back {
    margin-top: 20px !important;
  }
  .form-control {
    font-size: 0.65rem !important;
  }
}
</style>
