<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">공지 사항</h2>
      <div class="d-flex justify-content-between align-items-center">
        <div class="text-muted">Notice Management</div>
        <button
          class="btn btn-primary"
          v-if="isAdmin"
          @click="showModal('create')"
        >
          공지등록
        </button>
      </div>

      <!-- 테이블 보기 -->
      <table class="table mt-3" id="dataTable">
        <thead>
          <tr>
            <th>번호</th>
            <th>제목</th>
            <th>작성자</th>
            <th>작성일</th>
            <th></th> <!-- 새로 추가된 컬럼 -->
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="item in paginatedData"
            :key="item.id"
            @click="showModal('view', item)"
          >
            <td>{{ item.id }}</td>
            <td>{{ item.title }}</td>
            <td>{{ item.author }}</td>
            <td>{{ item.date }}</td>
            <td>
              <button
                class="btn btn-danger btn-sm"
                @click.stop="selectedItemId = item.id; confirmationModalVisible = true"
              >
                삭제
              </button>
            </td>
          </tr>
        </tbody>
      </table>

      <!-- 페이지네이션 -->
      <nav>
        <ul class="pagination justify-content-center page-botton">
          <li
            class="page-item"
            :class="{ disabled: currentPage === 1 }"
          >
            <a class="page-link" @click.prevent="changePage(currentPage - 1)">
              Previous
            </a>
          </li>
          <li
            v-for="page in totalPages"
            :key="page"
            class="page-item"
            :class="{ active: page === currentPage }"
          >
            <a class="page-link" @click.prevent="changePage(page)">
              {{ page }}
            </a>
          </li>
          <li
            class="page-item"
            :class="{ disabled: currentPage === totalPages }"
          >
            <a class="page-link" @click.prevent="changePage(currentPage + 1)">
              Next
            </a>
          </li>
        </ul>
      </nav>
    </div>

    <!-- 모달 창 -->
    <b-modal
      id="noticeModal"
      v-model="modalVisible"
      title="공지사항"
      @hide="resetModal"
    >
      <template #modal-header="{ hide }">
        <h5 class="modal-title">공지사항</h5>
        <button type="button" class="btn-close" @click="hide"></button>
      </template>
      <b-form>
        <b-form-group label="제목" label-for="noticeTitle">
          <b-form-input
            id="noticeTitle"
            v-model="form.title"
            :disabled="isViewMode"
            placeholder="제목을 입력하세요"
          ></b-form-input>
        </b-form-group>
        <b-form-group label="작성자" label-for="noticeAuthor">
          <b-form-input
            id="noticeAuthor"
            v-model="form.author"
            :disabled="isViewMode"
            placeholder="작성자를 입력하세요"
          ></b-form-input>
        </b-form-group>
        <b-form-group label="작성일" label-for="noticeDate">
          <b-form-input
            id="noticeDate"
            type="date"
            v-model="form.date"
            :disabled="true"
          ></b-form-input>
        </b-form-group>
        <b-form-group label="내용" label-for="noticeContent">
          <b-form-textarea
            id="noticeContent"
            v-model="form.content"
            :disabled="isViewMode"
            rows="4"
            placeholder="내용을 입력하세요"
          ></b-form-textarea>
        </b-form-group>
        <b-form-group v-if="!isAdmin" label="쿠키 동의" label-for="cookieConsent">
          <b-form-checkbox
            id="cookieConsent"
            v-model="form.cookieConsent"
          >
            해당 공지를 다시 띄우지 않음(쿠키 사용 동의)
          </b-form-checkbox>
        </b-form-group>
      </b-form>
      <template #modal-footer>
        <b-button variant="secondary" @click="modalVisible = false">닫기</b-button>
        <b-button
          v-if="isAdmin && !isViewMode"
          variant="primary"
          @click="saveNotice"
        >
          확인
        </b-button>
        <b-button
          v-if="isAdmin && isViewMode"
          variant="primary"
          @click="editNotice"
        >
          수정
        </b-button>
      </template>
    </b-modal>

    <AlertModal 
      :isVisible="confirmationModalVisible" 
      :disableBackgroundClose="false"
      title="저장 확인" 
      confirmText="확인" 
      cancelText="취소"
      @close="confirmationModalVisible = false" 
      @confirm="deleteNoticeConfirm"
      class="custom-alert-modal">
      <template #body>
        <p>삭제하시겠습니까?</p>
      </template>
    </AlertModal>


  </div>
</template>

<script setup>
import { ref, computed } from 'vue';
import { BModal, BButton, BForm, BFormGroup, BFormInput, BFormTextarea, BFormCheckbox } from 'bootstrap-vue-3';
import AlertModal from '@/components/modal/AlertModal.vue';

const isAdmin = ref(true);

const modalVisible = ref(false);
const isViewMode = ref(true);
const currentPage = ref(1);
const itemsPerPage = 8;
const form = ref({
  id: null,
  title: '',
  author: '',
  date: '',
  content: '',
  cookieConsent: false,
});

const confirmationModalVisible = ref(false);
const selectedItemId = ref(null);

const deleteNoticeConfirm = () => {
  confirmationModalVisible.value = false;
  // 저장된 ID로 삭제 진행
  deleteNotice(selectedItemId.value);
};

const data = ref([
  { id: 1, title: '공지사항 제목 1', author: '관리자', date: '2024-08-01', content: '공지사항 내용 1' },
  { id: 2, title: '공지사항 제목 2', author: '관리자', date: '2024-08-02', content: '공지사항 내용 2' },
  { id: 3, title: '공지사항 제목 3', author: '사용자', date: '2024-08-03', content: '공지사항 내용 3' },
  { id: 4, title: '공지사항 제목 4', author: '사용자', date: '2024-08-04', content: '공지사항 내용 4' },
  { id: 5, title: '공지사항 제목 5', author: '관리자', date: '2024-08-05', content: '공지사항 내용 5' },
  { id: 6, title: '공지사항 제목 6', author: '관리자', date: '2024-08-06', content: '공지사항 내용 6' },
  { id: 7, title: '공지사항 제목 7', author: '사용자', date: '2024-08-07', content: '공지사항 내용 7' },
  { id: 8, title: '공지사항 제목 8', author: '사용자', date: '2024-08-08', content: '공지사항 내용 8' },
  { id: 9, title: '공지사항 제목 9', author: '관리자', date: '2024-08-09', content: '공지사항 내용 9' },
  { id: 10, title: '공지사항 제목 10', author: '관리자', date: '2024-08-10', content: '공지사항 내용 10' },
  { id: 11, title: '공지사항 제목 11', author: '사용자', date: '2024-08-11', content: '공지사항 내용 11' },
  { id: 12, title: '공지사항 제목 12', author: '사용자', date: '2024-08-12', content: '공지사항 내용 12' },
]);

const paginatedData = computed(() => {
  const start = (currentPage.value - 1) * itemsPerPage;
  return data.value.slice(start, start + itemsPerPage);
});

const totalPages = computed(() => Math.ceil(data.value.length / itemsPerPage));

function getLocalDate() {
  const today = new Date();
  today.setMinutes(today.getMinutes() - today.getTimezoneOffset());
  return today.toISOString().split('T')[0];
}

function showModal(mode, item = null) {
  if (mode === 'create') {
    form.value = { id: null, title: '', author: '', date: getLocalDate(), content: '', cookieConsent: false };
    isViewMode.value = false;
  } else if (mode === 'view') {
    form.value = { ...item };
    isViewMode.value = !isAdmin.value;
  }
  modalVisible.value = true;
}

function resetModal() {
  modalVisible.value = false;
}

function saveNotice() {
  // 공지 저장 로직을 여기에 추가하세요
  resetModal();
}

function editNotice() {
  // 공지 수정 로직을 여기에 추가하세요
  resetModal();
}

function deleteNotice(id) {
  // 데이터에서 해당 ID를 가진 공지사항을 삭제
  data.value = data.value.filter((item) => item.id !== id);
}

function changePage(page) {
  if (page > 0 && page <= totalPages.value) {
    currentPage.value = page;
  }
}
</script>

<style scoped>
.pagination {
  margin-top: 30px !important;
}

/* 테이블 행에 마우스를 올렸을 때 배경색 변경 */
#dataTable tbody tr:hover td {
  background-color: #e7f1ff !important;
  /* font-weight: bold; 폰트를 볼드체로 변경
  text-decoration: underline; 텍스트에 밑줄 추가 */
}
/* #dataTable tbody tr:hover td a {
  color: #ffcc00 !important; 링크의 색상을 노란색으로 변경
  text-decoration: none; 링크의 밑줄을 제거
} */
</style>
