<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content" :class="{ 'content-expanded': sidebarVisible && !isMobile }">
      <h2 class="content-title">권범준님 영수증 상세 내역 조회</h2>
      <!-- 검색된 날짜 범위 표시 -->
      <div class="search-date-range mt-3 search-month-day">
        <p>날짜: [{{ filteredStartDate }} ~ {{ filteredEndDate }}]</p>
      </div>
      <!-- 뒤로가기 버튼 -->
      <button class="btn btn-secondary back-button" @click="goBack">뒤로가기</button>

      <!-- 테이블 보기 (큰 화면에서만 보임) -->
      <table class="table mt-3" v-if="!isMobile">
        <thead>
          <tr>
            <th>발생일</th>
            <th>인원수</th>
            <th>구분</th>
            <th>사유</th>
            <th>금액</th>
            <th>금액/인원수</th> <!-- 금액/인원수 컬럼 추가 -->
            <th>결제 상황</th>
          </tr>
        </thead>
        <tbody>
          <template v-for="item in paginatedData" :key="item.date">
            <tr @click="openSidebar(item)">
              <td>{{ item.date }}</td>
              <td>{{ item.people.length }}</td>
              <td>{{ item.type }}</td>
              <td>{{ item.reason }}</td>
              <td>{{ item.amount }}</td>
              <td>{{ calculateAmountPerPerson(item) }}</td>
              <td :class="getStatusClass(item.status)">{{ item.status }}</td>
            </tr>
          </template>
        </tbody>
      </table>

      <!-- 카드 레이아웃 보기 (작은 화면에서만 보임) -->
      <div class="card-layout" v-if="isMobile">
        <div class="card" v-for="item in paginatedData" :key="item.date">
          <div class="card-header">
            <p class="card-title">{{ item.date }}</p>
          </div>
          <div class="card-body">
            <p class="card-text"><strong>인원 : </strong>
              <span @click="togglePeopleList(item)" style="cursor: pointer;">{{ item.people.length }}명</span>
            </p>
            <div v-if="showPeopleList(item)">
              <div class="people-list">
                <div class="people-list-header">명단</div>
                <p v-for="person in item.people" :key="person.name" class="people-list-item">{{ person.name }} ({{
                  person.department }} - {{ person.team }})</p>
              </div>
              <hr />
            </div>
            <p class="card-text"><strong>구분/사유 : </strong> {{ item.type }} / {{ item.reason }}</p>
            <p class="card-text"><strong>금액 : </strong> {{ item.amount }}</p>
            <p class="card-text"><strong>금액/인원수 : </strong> {{ calculateAmountPerPerson(item) }}</p>
            <p>
              <strong class="card-text">영수증 사진 : </strong>
              <a class="card-text" @click.prevent="openPreviewModal(item.receipt)"
                style="cursor: pointer; color: blue;">{{
                  item.receiptName }}</a>
            </p>
            <p class="card-text">
              <strong>반려 사유: </strong> {{ item.rejectionReason || 'X' }}
            </p>
            <p class="card-text">
              <strong>결제 상황:</strong> <span :class="getStatusClass(item.status)">{{ item.status }}</span>
            </p>
          </div>
        </div>
      </div>


      <!-- 페이지네이션 버튼 -->
      <nav>
        <ul class="pagination justify-content-center" id="pagination">
          <li class="page-item" :class="{ active: page === currentPage }" v-for="page in totalPages" :key="page">
            <a class="page-link" href="#" @click.prevent="goToPage(page)">{{ page }}</a>
          </li>
        </ul>
      </nav>
    </div>

    <!-- 사이드바 추가 -->
    <div :class="['sidebar-wrapper', { 'sidebar-visible': sidebarVisible }]">
      <ReceiptSidebar 
        v-if="!isMobile" 
        :item="selectedItem" 
        :visible="sidebarVisible" 
        @close="closeSidebar"
        @preview-image="openPreviewModal" />
    </div>

    <!-- 이미지 미리보기 모달 -->
    <div v-if="isPreviewVisible" class="modal preview-modal" @click="closePreviewModalOnOutsideClick">
      <div
        class="preview-modal-content"
        @mousedown="startDrag"
        @mousemove="onDrag"
        @mouseup="endDrag"
        @mouseleave="endDrag"
      >
        <img
          :src="previewImage"
          :class="{ zoomed: isZoomed }"
          class="preview-modal-image"
          :style="{
            transform: isZoomed
              ? `translate(${zoomedPosition.x}px, ${zoomedPosition.y}px) scale(1.5)`
              : 'none',
            transformOrigin: `${zoomOrigin.x}px ${zoomOrigin.y}px`
          }"
          @dblclick="toggleZoom"
        />
      </div>
    </div>


  </div>
</template>

<script setup>
import { ref, computed, onMounted, watch } from 'vue';
import { useRouter } from 'vue-router'; // vue-router import
import ReceiptSidebar from './ReceiptSidebar.vue'; // 사이드바 컴포넌트 임포트
// import axios from 'axios';

// 검색된 데이터의 시작일과 종료일 계산
const filteredStartDate = ref('2024-08');
const filteredEndDate = ref('2024-09');

const selectedItem = ref(null);  // 사이드바에 표시할 선택된 항목
const sidebarVisible = ref(false);  // 사이드바 가시성 제어
const isMobile = ref(window.innerWidth <= 1150);
const currentPage = ref(1);
const itemsPerPage = ref(5);
import { usePreviewModal } from '@/utils/preview-modal';

const {
  isPreviewVisible,
  previewImage,
  isZoomed,
  zoomedPosition,
  zoomOrigin,
  openPreviewModal,
  toggleZoom,
  startDrag,
  onDrag,
  endDrag
} = usePreviewModal();

const data = ref([
  {
    date: '2024-08-01',
    type: '식비',
    reason: '야근',
    amount: '100,000원',
    receiptName: '영수증 예시',
    receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
    //receipt: '/img/receipt_example.jpg',
    status: '반려',
    rejectionReason: '미지원 항목으로 인한 반려',
    people: [
      { department: 'SI사업본부', team: '프레임워크', name: '권범준', position: '책임' },
      { department: 'SI사업본부', team: '프레임워크', name: '홍길동', position: '선임' },
    ],
  },
  {
    date: '2024-08-02',
    type: '식비',
    reason: '야근',
    amount: '110,000원',
    receiptName: '영수증 예시',
    receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
    //receipt: '/img/receipt_example.jpg',
    status: '신청중',
    rejectionReason: '',
    people: [
      { department: 'SI사업본부', team: '프레임워크', name: '권범준', position: '책임' },
      { department: 'SI사업본부', team: '프레임워크', name: '홍길동', position: '선임' },
      { department: 'SI사업본부', team: '프레임워크', name: '심청이', position: '선임' },
      { department: 'SI사업본부', team: '프레임워크', name: '똑순이', position: '선임' },
      { department: 'SI사업본부', team: '프레임워크', name: '복순이', position: '선임' },
      { department: 'SI사업본부', team: '프레임워크', name: '황순이', position: '선임' },
    ],
  },
  // 추가 데이터...
]);

// Vue 이전 페이지
const router = useRouter();
const goBack = () => {
  router.back(); // 뒤로가기
};

const closePreviewModalOnOutsideClick = (event) => {
  // 클릭된 요소가 모달 외부일 때만 미리보기 모달을 닫습니다.
  if (!event.target.classList.contains('preview-modal-image')) {
    isPreviewVisible.value = false;
  }
};

// 금액/인원수 계산
const calculateAmountPerPerson = (item) => {
  const amount = parseInt(item.amount.replace(/[^0-9]/g, ''), 10); // 금액에서 숫자만 추출
  const peopleCount = item.people.length;
  return peopleCount > 0 ? Math.floor(amount / peopleCount).toLocaleString() + ' 원' : 'N/A';
};

// 페이지네이션 데이터
const totalPages = computed(() => Math.ceil(data.value.length / itemsPerPage.value));
const paginatedData = computed(() => {
  const start = (currentPage.value - 1) * itemsPerPage.value;
  return data.value.slice(start, start + itemsPerPage.value);
});

const goToPage = (page) => {
  currentPage.value = page;
};

const updateIsMobile = () => {
  isMobile.value = window.innerWidth <= 1150;
};

const openSidebar = (item) => {
  if (!isMobile.value) {
    selectedItem.value = item;
    sidebarVisible.value = true;
  }
};

const closeSidebar = () => {
  sidebarVisible.value = false;
};

const getStatusClass = (status) => {
  if (status === '신청중') return 'status-pending';
  if (status === '승인') return 'status-approved';
  if (status === '반려') return 'status-rejected';
  return '';
};

const visiblePeopleLists = ref({});

const togglePeopleList = (item) => {
  if (visiblePeopleLists.value[item.date]) {
    delete visiblePeopleLists.value[item.date];
  } else {
    visiblePeopleLists.value[item.date] = true;
  }
};

const showPeopleList = (item) => {
  return visiblePeopleLists.value[item.date];
};


// 모바일로 전환 시 사이드바 자동 닫기
watch(isMobile, (newVal) => {
  if (newVal) {
    closeSidebar();
  }
});

onMounted(() => {
  window.addEventListener('resize', updateIsMobile);
  updateIsMobile();
});
</script>

<style scoped>
.content {
  transition: margin-right 0.3s ease;
}

.content-expanded {
  margin-right: 300px;
}

.search-month-day {
  font-weight: bold;
}

.back-button {
  padding: 0.3rem 0.6rem !important;
  font-size: 0.7rem;
  margin-bottom: 57px;
}

.card-layout {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.card {
  width: 100%;
  border: 1px solid #ddd;
  padding: 10px;
  border-radius: 5px;
}

.card-header {
  font-size: 1.2rem;
  font-weight: bold;
}

/* 사이드바 감싸는 wrapper 스타일 및 애니메이션 */
.sidebar-wrapper {
  position: fixed;
  right: 0;
  top: 0;
  width: 300px;
  height: 100%;
  transform: translateX(100%);
  /* 기본 상태: 화면 밖으로 */
  transition: transform 0.3s ease;
  /* 슬라이드 애니메이션 효과 */
  z-index: 1000;
}

/* 사이드바가 보일 때 적용되는 클래스 */
.sidebar-visible {
  transform: translateX(0);
  /* 화면 안으로 슬라이드 */
}

/* 사이드바 내용물 스타일 유지 */
.sidebar {
  width: 100%;
  height: 100%;
  background-color: #f9f9f9;
  border-left: 1px solid #ddd;
  padding: 20px;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
}

/* 상태별 색상 설정 */
.status-approved {
  color: green;
  /* 승인 상태는 초록색 */
  font-weight: bold;
  /* 폰트를 진하게 설정 */
}

.status-pending {
  color: blue;
  /* 신청 중 상태는 파란색 */
  font-weight: bold;
  /* 폰트를 진하게 설정 */
}

.status-rejected {
  color: red;
  /* 반려 상태는 빨간색 */
  font-weight: bold;
  /* 폰트를 진하게 설정 */
}

.status-closed {
  color: black;
  /* 회색 진하게 */
  font-weight: bold;
  /* 폰트를 진하게 설정 */
}

/* 인원 목록 스타일 추가 */
.people-list {
  font-size: 0.7rem;
  max-height: 150px;
  overflow-y: auto;
  margin-top: 10px;
  border-top: 1px solid #ddd;
  padding-top: 10px;
}

.people-list-header {
  font-weight: bold;
  margin-bottom: 5px;
}

.people-list-item {
  padding: 1px 0;
  margin: 0;
  margin-left: 10px;
}
</style>
