<template>
  <div class="content">
    <h2 class="content-title">프로젝트별 권한 부여</h2>
    <p class="content-sub-title">각 프로젝트별 사용자 권한을 관리하세요.</p>

    <!-- 부서, 팀 선택 (CSS Grid로 한 줄에 2개씩 배치) -->
    <div class="grid-container">
      <div class="grid-item">
        <label for="departmentSelect" class="form-label">부서 선택</label>
        <select id="departmentSelect" v-model="selectedDepartment" class="form-select" @change="onDepartmentChange">
          <option value="전체">전체</option>
          <option value="SI 사업본부">SI 사업본부</option>
          <option value="AI 사업본부">AI 사업본부</option>
          <option value="경영관리부">경영관리부</option>
        </select>
      </div>

      <div class="grid-item">
        <label for="teamSelect" class="form-label">팀 선택</label>
        <select id="teamSelect" v-model="selectedTeam" class="form-select">
          <option v-for="team in teams" :key="team" :value="team">{{ team }}</option>
        </select>
      </div>

      <div class="grid-item">
        <label for="projectSelect" class="form-label">프로젝트 선택</label>
        <select id="projectSelect" v-model="selectedProject" class="form-select">
          <option v-for="project in projects" :key="project" :value="project">{{ project }}</option>
        </select>
      </div>

      <div class="grid-item">
        <label for="userSearch" class="form-label">사용자 검색</label>
        <input
          type="text"
          id="userSearch"
          v-model="searchTerm"
          class="form-control"
          placeholder="이름을 입력하세요"
          @keyup.enter="filterUsers"
        />
      </div>
    </div>

    <!-- 권한 테이블 -->
    <div v-if="selectedProject" class="table-section">
      <table class="table table-bordered">
        <thead>
          <tr>
            <th>사용자 이름</th>
            <th>부서</th>
            <th>팀</th>
            <th>권한</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="user in paginatedUsers" :key="user.id">
            <td>{{ user.name }}</td>
            <td>{{ user.department }}</td>
            <td>{{ user.team }}</td>
            <td>
              <select v-model="user.permission" class="form-select">
                <option :value="null" disabled>없음</option>
                <option v-for="permission in projectPermissions" :key="permission" :value="permission">
                  {{ permission }}
                </option>
              </select>
            </td>
          </tr>
        </tbody>
      </table>

      <!-- 페이지네이션 -->
      <nav class="d-flex justify-content-center">
        <ul class="pagination">
          <li class="page-item" :class="{ disabled: currentPage === 1 }" @click="prevPage">
            <a class="page-link" href="#">이전</a>
          </li>
          <li class="page-item" v-for="page in totalPages" :key="page" :class="{ active: currentPage === page }">
            <a class="page-link" href="#" @click.prevent="goToPage(page)">{{ page }}</a>
          </li>
          <li class="page-item" :class="{ disabled: currentPage === totalPages }" @click="nextPage">
            <a class="page-link" href="#">다음</a>
          </li>
        </ul>
      </nav>

      <!-- 저장 버튼 -->
      <div class="text-end mt-4 save-button">
        <button class="btn btn-primary" @click="savePermissions">저장</button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, watch } from 'vue';

// 프로젝트 목록을 배열로 정의 (문자열)
const projects = ref(['영수증 관리', '개인정보 관리']);

// 부서와 팀 정보
const departmentTeams = {
  '전체': ['전체'],
  'SI 사업본부': ['전체', '프레임워크 팀'],
  'AI 사업본부': ['전체', 'RPA', '클라우드', '빅데이터'],
  '경영관리부': ['전체', '국내 영업팀', '해외 영업팀']
};

// 권한 목록 더미 데이터
const permissionLevels = {
  '영수증 관리': ['신청자', '승인자', '관리자'],
  '개인정보 관리': ['사용자', '관리자']
};

// 사용자 더미 데이터 - 최소 한 명씩 나오도록 설정
const users = ref([
  { id: 1, name: '사용자A', department: 'SI 사업본부', team: '프레임워크 팀', project: '영수증 관리', permission: null },
  { id: 2, name: '사용자B', department: 'SI 사업본부', team: '프레임워크 팀', project: '영수증 관리', permission: '신청자' },
  { id: 3, name: '사용자C', department: 'SI 사업본부', team: '프레임워크 팀', project: '영수증 관리', permission: '관리자' },
  { id: 4, name: '사용자D', department: 'SI 사업본부', team: '프레임워크 팀', project: '영수증 관리', permission: '사용자' },
  { id: 5, name: '사용자E', department: 'SI 사업본부', team: '프레임워크 팀', project: '영수증 관리', permission: null },
  { id: 6, name: '사용자F', department: 'SI 사업본부', team: '프레임워크 팀', project: '영수증 관리', permission: null },
  { id: 7, name: '사용자G', department: 'SI 사업본부', team: '프레임워크 팀', project: '영수증 관리', permission: null },
  { id: 1, name: '사용자A', department: 'SI 사업본부', team: '프레임워크 팀', project: '개인정보 관리', permission: '사용자' },
  { id: 2, name: '사용자B', department: 'SI 사업본부', team: '프레임워크 팀', project: '개인정보 관리', permission: '관리자' },
  { id: 3, name: '사용자C', department: 'SI 사업본부', team: '프레임워크 팀', project: '개인정보 관리', permission: null },
  { id: 4, name: '사용자D', department: 'SI 사업본부', team: '프레임워크 팀', project: '개인정보 관리', permission: null },
  { id: 5, name: '사용자E', department: 'SI 사업본부', team: '프레임워크 팀', project: '개인정보 관리', permission: null },
  { id: 6, name: '사용자F', department: 'SI 사업본부', team: '프레임워크 팀', project: '개인정보 관리', permission: null },
  { id: 7, name: '사용자G', department: 'SI 사업본부', team: '프레임워크 팀', project: '개인정보 관리', permission: null },
]);

// 선택된 항목들
const selectedProject = ref(null);
const selectedDepartment = ref('전체');
const selectedTeam = ref('전체');
const searchTerm = ref('');

// 페이징 관련 상태
const currentPage = ref(1);
const itemsPerPage = ref(5);

// 선택된 부서에 따른 팀 목록 필터링
const teams = ref(departmentTeams['전체']);
watch(selectedDepartment, (newDepartment) => {
  teams.value = departmentTeams[newDepartment] || ['전체'];
  selectedTeam.value = '전체';
  currentPage.value = 1; // 부서가 변경되면 페이지를 1로 초기화
});

// 선택된 프로젝트에 따른 권한 목록 필터링
const projectPermissions = computed(() => {
  return permissionLevels[selectedProject.value] || [];
});

// 검색된 사용자 목록 필터링
const filteredUsers = computed(() => {
  return users.value.filter((user) => {
    const matchesProject = user.project === selectedProject.value;
    const matchesDepartment = selectedDepartment.value === '전체' || user.department === selectedDepartment.value;
    const matchesTeam = selectedTeam.value === '전체' || user.team === selectedTeam.value;
    const matchesSearch = user.name.includes(searchTerm.value);
    return matchesProject && matchesDepartment && matchesTeam && matchesSearch;
  });
});

// 페이징 처리된 사용자 목록
const paginatedUsers = computed(() => {
  const start = (currentPage.value - 1) * itemsPerPage.value;
  const end = start + itemsPerPage.value;
  return filteredUsers.value.slice(start, end);
});

// 전체 페이지 수 계산
const totalPages = computed(() => {
  return Math.ceil(filteredUsers.value.length / itemsPerPage.value);
});

// 페이지 전환 함수
const goToPage = (page) => {
  if (page >= 1 && page <= totalPages.value) {
    currentPage.value = page;
  }
};

const prevPage = () => {
  if (currentPage.value > 1) {
    currentPage.value--;
  }
};

const nextPage = () => {
  if (currentPage.value < totalPages.value) {
    currentPage.value++;
  }
};

// 사용자에게 권한 저장
const savePermissions = () => {
  console.log('권한 저장:', filteredUsers.value);
};

// 사용자 필터링 (검색 기능)
const filterUsers = () => {
  console.log(`검색어: ${searchTerm.value}`);
};
</script>

<style scoped>
.grid-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

.grid-item {
  display: flex;
  flex-direction: column;
}

.form-control, .form-select {
  width: 100%;
}

.table-section {
  margin-top: 30px;
  width: 100%;
}

.table {
  width: 100%;
}

.table th, 
.table td {
  padding: 10px;
  text-align: left;
}

.text-end {
  text-align: right;
}

.save-button {
  margin-top: 0px !important;
  margin-bottom: 50px;
}

@media (max-width: 768px) {
  .grid-container {
    grid-template-columns: repeat(2, 1fr);
  }
  .table {
    font-size: 0.75rem;
  }
}
</style>
