<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content">
      <h2 class="content-title">년도별 영수증 요약</h2>
      <p class="content-sub-title">년도별 영수증 현황 요약 페이지</p>

      <div class="d-flex justify-content-between align-items-center mb-3">
        <div class="d-flex align-items-center">
          <select class="form-control w-auto" v-model="selectedYear" @change="fetchData">
            <option>2024</option>
            <option>2023</option>
            <option>2022</option>
          </select>
          <label class="me-2">&nbsp; 년도</label>
        </div>
      </div>
      
      <!-- 테이블 보기 -->
      <table class="table mt-3" id="dataTable">
        <thead>
          <tr>
            <th>월</th>
            <th>신청금</th>
            <th>승인금액</th>
            <th>반려금액</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in data" :key="index">
            <td>{{ item.month }}</td>
            <td>{{ item.requested.toLocaleString() }}원</td>
            <td>{{ item.approved.toLocaleString() }}원</td>
            <td>{{ item.rejected.toLocaleString() }}원</td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <th>총 합계</th>
            <th>{{ totalRequested.toLocaleString() }}원</th>
            <th>{{ totalApproved.toLocaleString() }}원</th>
            <th>{{ totalRejected.toLocaleString() }}원</th>
          </tr>
        </tfoot>
      </table>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';

// 상태 정의
const selectedYear = ref('2024');
const data = ref([]);
const totalRequested = ref(0);
const totalApproved = ref(0);
const totalRejected = ref(0);

// 더미 데이터
const dummyData = [
  { month: '1월', requested: 1000000, approved: 800000, rejected: 200000 },
  { month: '2월', requested: 1200000, approved: 1000000, rejected: 200000 },
  { month: '3월', requested: 800000, approved: 700000, rejected: 100000 },
  { month: '4월', requested: 1500000, approved: 1400000, rejected: 100000 },
  { month: '5월', requested: 1300000, approved: 1200000, rejected: 100000 },
  // 추가 데이터...
];

// 데이터 가져오기 함수
const fetchData = () => {
  // 더미 데이터로 시뮬레이션
  data.value = dummyData.filter(() => {
    // 모든 연도를 표시하므로 필터링 필요 없음
    return true;
  });

  // 총 합계 계산
  totalRequested.value = data.value.reduce((sum, item) => sum + item.requested, 0);
  totalApproved.value = data.value.reduce((sum, item) => sum + item.approved, 0);
  totalRejected.value = data.value.reduce((sum, item) => sum + item.rejected, 0);
};

// 컴포넌트가 마운트되면 데이터 가져오기
onMounted(fetchData);
</script>

