<template>
  <!-- transition 태그 추가 및 모달 감싸기 -->
  <transition name="fade">
    <div v-if="isVisible" class="custom-modal-pop-up" @click.self="handleBackgroundClick">
      <div class="custom-modal-pop-up-dialog">
        <div class="custom-modal-pop-up-content">
          <div class="custom-modal-pop-up-header">
            <h5>{{ title }}</h5>
          </div>
          <div class="custom-modal-pop-up-body">
            <slot name="body"></slot>
          </div>
          <div class="custom-modal-pop-up-footer">
            <!-- 취소 버튼: cancelText가 있을 때만 렌더링 -->
            <button v-if="cancelText" type="button" class="btn btn-secondary" @click="closeModal">{{ cancelText }}</button>
            <button type="button" class="btn btn-primary" @click="$emit('confirm')">{{ confirmText }}</button>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script setup>
import { defineProps, defineEmits } from 'vue';

const props = defineProps({
  isVisible: Boolean,
  title: {
    type: String,
    default: 'Modal Title',
  },
  confirmText: {
    type: String,
    default: 'Confirm',
  },
  cancelText: {
    type: String,
    default: '', // 기본값을 빈 문자열로 설정
  },
  // 백그라운드 클릭을 허용할지 제어하는 prop
  disableBackgroundClose: {
    type: Boolean,
    default: false,
  },
});

// 백그라운드 클릭 시 모달 닫힘을 제어하는 함수
const handleBackgroundClick = () => {
  if (!props.disableBackgroundClose) {
    closeModal();
  }
};

const emits = defineEmits(['confirm', 'close']);

const closeModal = () => {
  emits('close');
};
</script>

<style scoped>
/* 기존 스타일 */
.custom-modal-pop-up {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1050;
}

.custom-modal-pop-up-dialog {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
}

.custom-modal-pop-up-content {
  background-color: #fff;
  border-radius: 8px;
  padding: 20px;
  width: 100%;
  max-width: 400px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
}

.custom-modal-pop-up-header h5 {
  font-size: 1rem;
  text-align: left;
  margin-bottom: 20px;
}

.custom-modal-pop-up-body {
  padding: 10px 0;
  text-align: left;
  font-size: 0.9rem;
}

.custom-modal-pop-up-footer {
  display: flex;
  justify-content: flex-end;
}

.custom-modal-pop-up-footer .btn {
  min-width: 80px;
  margin-left: 10px;
}

.custom-modal-pop-up-header {
  border-bottom: none;
}

.custom-modal-pop-up-footer {
  border-top: none;
  padding: 0;
  margin: 0;
}

/* 애니메이션 효과 */
.fade-enter-active {
  transition: opacity 0.2s ease;
}

.fade-enter-from {
  opacity: 0;
}

/* 반응형: 모바일 환경에서 폰트 크기 조정 */
@media (max-width: 768px) {
  .custom-modal-pop-up-content {
    width: 70%;
  }

  .custom-modal-pop-up-footer .btn {
    font-size: 0.6rem !important; /* 모바일에서 버튼 글자 크기 */
    padding: 0.2rem 0.4rem !important; /* 모바일에서 버튼 크기 */
    min-width: 50px;
  }

  .custom-modal-pop-up-header h5 {
    font-size: 0.8rem;
    margin-bottom: 8px;
  }

  .custom-modal-pop-up-body {
    font-size: 0.75rem;
  }
}
</style>
