<template>
  <div>
    <!-- Main Content Area -->
    <div class="content">
      <h2 class="content-title">
        {{ searchNameResult ? `${searchNameResult}님의 실적 관리` : '이름을 입력하여 실적을 검색하세요' }}
      </h2>
      <p class="content-sub-title">실적 등록 및 확인 페이지</p>

      <div class="d-flex justify-content-between align-items-center" style="color: red; font-weight: bold;">
        <p></p>
        <p>예시 이름 "권범준"으로 검색</p>
      </div>

      <div class="d-flex justify-content-between align-items-center">
        <button v-if="isAdmin && searchNameResult" class="btn btn-primary performance-save-button" @click="openModal">
          실적 추가
        </button>
        <p></p>
        <div class="d-flex justify-content-between mb-3">
          <div>
            <input type="text" class="form-control input-search-name" v-model="searchName" placeholder="이름 입력"
              @keyup.enter="filterData" />
          </div>
          <button class="btn btn-primary bnt-search-name" @click="filterData">
            검색
          </button>
        </div>
      </div>

      <!-- 검색을 하지 않았을 때 이미지 표시 -->
      <div v-if="!searchNameResult" class="d-flex justify-content-center mt-5">
        <img src="https://www.kpcnc.co.kr/src/img/main_slider1.png" alt="검색을 하지 않았을 때 표시할 이미지"
          class="placeholder-image" />
      </div>

      <!-- Table View (Visible on larger screens) -->
      <table class="table mt-3" id="dataTable" v-if="!isMobile && searchNameResult">
        <thead>
          <tr>
            <th>시작일</th>
            <th>종료일</th>
            <th>유형</th>
            <th>성과 주제</th>
            <th>성과</th>
            <th>상태</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in paginatedData" :key="item.fromDate" @click="openEditModal(item)">
            <td>{{ item.fromDate }}</td>
            <td>{{ item.toDate }}</td>
            <td>{{ item.workType }}</td>
            <td>{{ item.performanceTitle }}</td>
            <td>{{ item.performance }}</td>
            <td :class="getStatusClass(item.status)">{{ item.status }}</td>
          </tr>
        </tbody>
      </table>

      <!-- Card Layout View (Visible on smaller screens) -->
      <div class="card-layout" v-if="isMobile && searchNameResult">
        <div class="card" v-for="item in paginatedData" :key="item.fromDate" @click="openEditModal(item)">
          <div class="card-header">
            <p class="card-title">{{ item.fromDate }} - {{ item.toDate }}</p>
          </div>
          <div class="card-body">
            <p class="card-text"><strong>유형: </strong> {{ item.workType }}</p>
            <p class="card-text"><strong>성과 주제: </strong> {{ item.performanceTitle }}</p>
            <p class="card-text"><strong>성과: </strong> {{ item.performance }}</p>
            <p class="card-text">
              <strong>상태: </strong>
              <span :class="getStatusClass(item.status)">{{ item.status }}</span>
            </p>
          </div>
        </div>
      </div>

      <!-- Pagination Buttons -->
      <nav>
        <ul class="pagination justify-content-center" id="pagination">
          <li class="page-item" :class="{ active: page === currentPage }" v-for="page in totalPages" :key="page">
            <a class="page-link" href="#" @click.prevent="goToPage(page)">
              {{ page }}
            </a>
          </li>
        </ul>
      </nav>
    </div>

    <!-- Modal for Adding Performance -->
    <div class="modal fade" id="performanceModal" tabindex="-1" role="dialog" aria-labelledby="performanceModalLabel"
      aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="performanceModalLabel">실적 추가</h5>
            <button type="button" class="close" aria-label="Close" @click="closeModal">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <form @submit.prevent="submitPerformance">
              <!-- 시작일과 종료일을 한 줄로 배치 -->
              <div class="d-flex">
                <div class="form-group w-50 me-2">
                  <label for="fromDate">시작일</label>
                  <input type="date" class="form-control" id="fromDate" v-model="formData.fromDate" />
                </div>
                <div class="form-group w-50">
                  <label for="toDate">종료일</label>
                  <input type="date" class="form-control" id="toDate" v-model="formData.toDate" />
                </div>
              </div>
              <br />
              <!-- 유형과 상태를 한 줄로 배치 -->
              <div class="d-flex">
                <div class="form-group me-2" style="flex: 4 3;">
                  <label for="workType">유형</label>
                  <select class="form-control" id="workType" v-model="formData.workType">
                    <option>프로젝트</option>
                    <option>회의</option>
                    <option>보고서 작성</option>
                  </select>
                </div>
                <div class="form-group" style="flex: 4 1;">
                  <label for="status">상태</label>
                  <select class="form-control" id="status" v-model="formData.status">
                    <option>진행 중</option>
                    <option>완료</option>
                    <option>대기 중</option>
                  </select>
                </div>
              </div>
              <br />
              <div class="form-group">
                <label for="performanceTitle">성과 주제</label>
                <input type="text" class="form-control" id="performanceTitle" placeholder="성과 주제를 입력해주세요"
                  v-model="formData.performanceTitle" />
              </div>
              <br />
              <div class="form-group">
                <label for="performance">성과</label>
                <textarea class="form-control" id="performance" placeholder="성과를 입력해주세요" v-model="formData.performance"
                  rows="4"></textarea>
              </div>
              <br />
              <div class="modal-footer">
                <button type="button" class="btn btn-secondary" @click="closeModal">취소</button>
                <button type="submit" class="btn btn-primary">저장</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>


    <!-- 수정 모달 -->
    <div class="modal fade" id="editModal" tabindex="-1" role="dialog" aria-labelledby="editModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="editModalLabel">실적 수정</h5>
            <button type="button" class="close" aria-label="Close" @click="closeEditModal">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <form @submit.prevent="submitEdit">
              <!-- 기존 실적 추가 모달과 동일한 내용 -->
              <!-- 시작일과 종료일을 한 줄로 배치 -->
              <div class="d-flex">
                <div class="form-group w-50 me-2">
                  <label for="editFromDate">시작일</label>
                  <input type="date" class="form-control" id="editFromDate" v-model="editData.fromDate" />
                </div>
                <div class="form-group w-50">
                  <label for="editToDate">종료일</label>
                  <input type="date" class="form-control" id="editToDate" v-model="editData.toDate" />
                </div>
              </div>
              <br />
              <!-- 유형과 상태를 한 줄로 배치 -->
              <div class="d-flex">
                <div class="form-group me-2" style="flex: 4 3;">
                  <label for="editWorkType">유형</label>
                  <select class="form-control" id="editWorkType" v-model="editData.workType">
                    <option>프로젝트</option>
                    <option>회의</option>
                    <option>보고서 작성</option>
                  </select>
                </div>
                <div class="form-group" style="flex: 4 1;">
                  <label for="editStatus">상태</label>
                  <select class="form-control" id="editStatus" v-model="editData.status">
                    <option>진행 중</option>
                    <option>완료</option>
                    <option>대기 중</option>
                  </select>
                </div>
              </div>
              <br />
              <div class="form-group">
                <label for="editPerformanceTitle">성과 주제</label>
                <input type="text" class="form-control" id="editPerformanceTitle" placeholder="성과 주제를 입력해주세요" v-model="editData.performanceTitle" />
              </div>
              <br />
              <div class="form-group">
                <label for="editPerformance">성과</label>
                <textarea class="form-control" id="editPerformance" placeholder="성과를 입력해주세요" v-model="editData.performance" rows="4"></textarea>
              </div>
              <br />
              <div class="modal-footer">
                <button type="button" class="btn btn-secondary" @click="closeEditModal">취소</button>
                <button type="button" class="btn btn-danger" @click="removeItem(editData)">삭제</button>
                <button type="submit" class="btn btn-primary">수정</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>


  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
import axios from 'axios';
import * as bootstrap from 'bootstrap';

const searchName = ref('');
const searchNameResult = ref('');
const data = ref([]);
const editData = ref({}); // 수정할 데이터 상태

const currentPage = ref(1);
const itemsPerPage = ref(5);
const isMobile = ref(window.innerWidth <= 1150);
const userRole = ref('admin');
const isAdmin = computed(() => userRole.value === 'admin');

const formData = ref({
  fromDate: '',
  toDate: '',
  workType: '프로젝트',
  performanceTitle: '',
  performance: '',
  status: '진행 중', // 초기 상태 값
});

const totalPages = computed(() => Math.ceil(data.value.length / itemsPerPage.value));

const paginatedData = computed(() => {
  const startIndex = (currentPage.value - 1) * itemsPerPage.value;
  return data.value.slice(startIndex, startIndex + itemsPerPage.value);
});

const goToPage = (page) => {
  currentPage.value = page;
};

const openModal = () => {
  const modal = document.getElementById('performanceModal');
  const bootstrapModal = new bootstrap.Modal(modal);
  bootstrapModal.show();
};

const closeModal = () => {
  const modal = document.getElementById('performanceModal');
  const bootstrapModal = bootstrap.Modal.getInstance(modal);
  bootstrapModal.hide();
};

const openEditModal = (item) => {
  editData.value = { ...item }; // 데이터 복사
  const modal = document.getElementById('editModal');
  const bootstrapModal = new bootstrap.Modal(modal);
  bootstrapModal.show();
};

const closeEditModal = () => {
  const modal = document.getElementById('editModal');
  const bootstrapModal = bootstrap.Modal.getInstance(modal);
  bootstrapModal.hide();
};

const submitPerformance = () => {
  console.log('Performance Submitted:', formData.value);
  closeModal();
};

const submitEdit = () => {
  console.log('Performance Edited:', editData.value);
  closeEditModal();
};

const updateIsMobile = () => {
  isMobile.value = window.innerWidth <= 1150;
};

const getStatusClass = (status) => {
  if (status === '진행 중') return 'text-primary'; // Blue
  if (status === '완료') return 'text-success'; // Green
  if (status === '대기 중') return 'text-warning'; // Orange
  return '';
};

const removeItem = (item) => {
  data.value = data.value.filter((entry) => entry !== item);
  closeEditModal();
};

// 데이터 필터링 및 서버 요청
const filterData = async () => {
  if (searchName.value.trim()) {
    if (searchName.value === '권범준') {
      // 더미 데이터 설정
      data.value = [
        { fromDate: '2024-05-01', toDate: '2024-05-15', workType: '프로젝트', performanceTitle: '기업은행 프로젝트', performance: '＊억 프로젝트 진행 완료', status: '완료' },
        { fromDate: '2024-07-15', toDate: '2024-07-30', workType: '프로젝트', performanceTitle: '내부 포탈 프로젝트 개발', performance: '내부 포탈을 개발하여 ＊＊ 가치를 창출', status: '진행 중' },
        { fromDate: '2024-08-30', toDate: '2024-09-10', workType: '프로젝트', performanceTitle: '삼성카드 프로젝트', performance: '＊억 프로젝트 진행 완료', status: '대기 중' }
      ];
      searchNameResult.value = '권범준'; // 검색 결과로 권범준 설정
    } else {
      try {
        const response = await axios.get('/api/performance', {
          params: { name: searchName.value }
        });
        data.value = response.data; // 서버에서 받은 데이터를 data에 저장
        searchNameResult.value = searchName.value; // 검색한 이름으로 제목 업데이트
      } catch (error) {
        console.error('에러 발생:', error);
        alert('데이터를 가져오는 데 실패했습니다.');
      }
    }
  } else {
    alert('이름을 입력해 주세요.');
    data.value = []; // 검색어가 없을 때는 데이터를 비워둠
    searchNameResult.value = ''; // 검색 결과 초기화
  }
};

onMounted(() => {
  window.addEventListener('resize', updateIsMobile);
});
</script>


<style scoped>
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.close {
  font-size: 1.5rem;
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.performance-save-button {
  background-color: #375a7f;
  font-size: 0.875rem;
  padding: 0.4rem 0.75rem;
}

.bnt-search-name {
  background-color: #375a7f;
}

#dataTable tbody tr:hover td {
  background-color: #e7f1ff !important;
}

.placeholder-image {
  width: 500px;
  margin-bottom: 30px;
  height: auto;
  opacity: 0.8;
}

@media (max-width: 1150px) {
  .performance-save-button {
    margin-bottom: 10px;
  }
}

@media (max-width: 768px) {
  .close {
    font-size: 1rem !important;
  }

  label {
    font-size: 0.85rem;
  }

  .placeholder-image {
    width: 400px;
    margin-bottom: 30px;
    margin-top: 50px;
    height: auto;
    opacity: 0.8;
  }

  .performance-save-button {
    margin-bottom: 0;
    font-size: 0.63rem;
    padding: 0.3rem 0.6rem;
  }

  .align-items-center {
    margin-bottom: 10px;
  }

  .text-muted,
  .card-text {
    font-size: 0.75rem;
    margin-bottom: 7px;
  }

  .input-search-name {
    font-size: 0.75rem;
    padding: 0.3rem 0.6rem;
  }

  @media (max-width: 401px) {
    .placeholder-image {
      max-width: 100%;
      margin-bottom: 30px;
      margin-top: 50px;
      height: auto;
      opacity: 0.8;
    }
  }
}
</style>
