<template>
  <div>
    <!-- 메인 콘텐츠 영역 -->
    <div class="content" :class="{ 'content-expanded': sidebarVisible && !isMobile }">
      <h2 class="content-title">영수증 등록</h2>
      <p class="content-sub-title">영수증 등록 페이지</p>

      <div class="d-flex justify-content-end align-items-center">
        <button class="btn btn-primary search-button" @click="openModal">영수증 등록</button>
      </div>

      <!-- 테이블 보기 (큰 화면에서만 보임) -->
      <table class="table mt-3" v-if="!isMobile">
        <thead>
          <tr>
            <th>발생일</th>
            <th>구분</th>
            <th>인원수</th>
            <th>사유</th>
            <th>금액</th>
            <th>금액/인원수</th>
          </tr>
        </thead>
        <tbody>
          <template v-for="item in paginatedData" :key="item.date">
            <tr @click="openSidebar(item)">
              <td>{{ item.date }}</td>
              <td>{{ item.type }}</td>
              <td>{{ item.people.length }}</td>
              <td>{{ item.reason }}</td>
              <td>{{ item.amount }}</td>
              <td>{{ calculateAmountPerPerson(item) }}</td>
            </tr>
          </template>
        </tbody>
      </table>

      <!-- 카드 레이아웃 보기 (작은 화면에서만 보임) -->
      <div class="card-layout" v-if="isMobile">
        <div class="card" v-for="item in paginatedData" :key="item.date">
          <div class="card-header">
            <p class="card-title">{{ item.date }}</p>
          </div>
          <div class="card-body">
            <p class="card-text"><strong>인원 : </strong>
              <span @click="togglePeopleList(item)" style="cursor: pointer;">{{ item.people.length }}명</span>
            </p>
            <div v-if="showPeopleList(item)">
              <div class="people-list">
                <div class="people-list-header">명단</div>
                <p v-for="person in item.people" :key="person.name" class="people-list-item">{{ person.name }} ({{
                  person.department }} - {{ person.team }})</p>
              </div>
              <hr />
            </div>
            <p class="card-text"><strong>구분/사유 : </strong> {{ item.type }} / {{ item.reason }}</p>
            <p class="card-text"><strong>금액 : </strong> {{ item.amount }}</p>
            <p class="card-text"><strong>금액/인원수 : </strong> {{ calculateAmountPerPerson(item) }}</p>
            <p>
              <strong class="card-text">영수증 사진 : </strong>
              <a class="card-text" @click.prevent="openPreviewModal(item.receipt)"
                style="cursor: pointer; color: blue;">{{
                  item.receiptName }}</a>
            </p>
          </div>
        </div>
      </div>

      <!-- 페이지네이션 버튼 -->
      <nav>
        <ul class="pagination justify-content-center" id="pagination">
          <li class="page-item" :class="{ active: page === currentPage }" v-for="page in totalPages" :key="page">
            <a class="page-link" href="#" @click.prevent="goToPage(page)">{{ page }}</a>
          </li>
        </ul>
      </nav>
    </div>

    <!-- 모달 창 추가 -->
    <div class="modal fade" id="receiptModal" tabindex="-1" role="dialog" aria-labelledby="receiptModalLabel"
      aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="receiptModalLabel">영수증 등록</h5>
            <!-- <button type="button" class="close" aria-label="Close" @click="closeModal">
              <span aria-hidden="true">&times;</span>
            </button> -->
          </div>
          <div class="modal-body">
            <form @submit.prevent="submitReceipt">

              <!-- 이미지 등록 -->
              <div class="form-group">
                <label for="receiptImage">영수증 사진</label>
                <div class="mt-2">
                  <input type="file" class="form-control-file" id="receiptImage" @change="handleFileChange" />
                </div>
              </div>

              <!-- 영수증 발행일 -->
              <div class="form-group">
                <label for="date">영수증 발행일</label>
                <input type="date" class="form-control" id="date" v-model="formData.date" />
              </div>

              <!-- 구분 | 금액 -->
              <div class="form-row align-items-center">
                <div class="form-group col">
                  <label for="type">구분</label>
                  <select class="form-control" id="type" v-model="formData.type">
                    <option>식비</option>
                    <option>교통비</option>
                    <option>회식</option>
                    <option>기타</option>
                  </select>
                </div>
                <div class="form-group col">
                  <label for="amount">금액</label>
                  <div class="input-group">
                    <input type="text" class="form-control" id="amount" placeholder="내용을 입력해주세요"
                      v-model="formData.amount" />
                    <div class="input-group-append">
                      <span class="input-group-text">원</span>
                    </div>
                  </div>
                </div>
              </div>

              <!-- 사용자 검색 필드 -->
              <div class="form-group">
                <label for="userSearch">사용자 검색 <p style="font-size: 0.8rem; color: red;">테스트 '권범','홍길동','이영희'</p>
                </label>
                <input type="text" class="form-control" id="userSearch" placeholder="사용자 이름 검색" v-model="userSearch"
                  @input="searchUsers" @keydown.enter.prevent />
                <!-- 검색 결과 목록 -->
                <ul v-if="searchResults.length > 0" class="list-group mt-2">
                  <li v-for="(user, index) in searchResults" :key="index" class="list-group-item"
                    @click="selectUser(user)">
                    {{ user.name }} ({{ user.department }} - {{ user.team }} - {{ user.position }})
                  </li>
                </ul>
              </div>

              <!-- 추가된 인원 목록 -->
              <div v-if="formData.people.length > 0" class="mt-3">
                <h6>추가된 인원 목록</h6>
                <ul class="list-group">
                  <li v-for="(person, index) in formData.people" :key="index" class="list-group-item">
                    {{ person.name }} - {{ person.department }} - {{ person.team }} - {{ person.position }}
                  </li>
                </ul>
              </div>

              <!-- 사유 -->
              <div class="form-group reason">
                <label for="reason">사유</label>
                <input type="text" class="form-control" id="reason" placeholder="내용을 입력해주세요"
                  v-model="formData.reason" />
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-secondary" @click="closeModal">취소</button>
                <button type="submit" class="btn btn-primary">등록</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>

    <AlertModal 
      :isVisible="confirmationModalVisible" 
      :disableBackgroundClose="true"
      title="저장 확인" 
      confirmText="확인" 
      cancelText="취소"
      @close="confirmationModalVisible = false" 
      @confirm="saveUserDetails"
      class="custom-alert-modal">
      <template #body>
        <p>등록하시겠습니까?</p>
      </template>
    </AlertModal>

    <!-- 사이드바 추가 -->
    <div :class="['sidebar-wrapper', { 'sidebar-visible': sidebarVisible }]">
      <ReceiptSidebar 
        v-if="!isMobile" 
        :item="selectedItem" 
        :visible="sidebarVisible" 
        @close="closeSidebar"
        @preview-image="openPreviewModal" />
    </div>

    <!-- 이미지 미리보기 모달 -->
    <div v-if="isPreviewVisible" class="modal preview-modal" @click="closePreviewModalOnOutsideClick">
      <div
        class="preview-modal-content"
        @mousedown="startDrag"
        @mousemove="onDrag"
        @mouseup="endDrag"
        @mouseleave="endDrag"
      >
        <img
          :src="previewImage"
          :class="{ zoomed: isZoomed }"
          class="preview-modal-image"
          :style="{
            transform: isZoomed
              ? `translate(${zoomedPosition.x}px, ${zoomedPosition.y}px) scale(1.5)`
              : 'none',
            transformOrigin: `${zoomOrigin.x}px ${zoomOrigin.y}px`
          }"
          @dblclick="toggleZoom"
        />
      </div>
    </div>

  </div>
</template>

<script setup>
import { ref, computed, onMounted } from 'vue';
import * as bootstrap from 'bootstrap';
// import axios from 'axios'; // Axios 임포트
import ReceiptSidebar from './ReceiptSidebar.vue';
import AlertModal from '@/components/modal/AlertModal.vue';
import { usePreviewModal } from '@/utils/preview-modal';

const {
  isPreviewVisible,
  previewImage,
  isZoomed,
  zoomedPosition,
  zoomOrigin,
  openPreviewModal,
  toggleZoom,
  startDrag,
  onDrag,
  endDrag
} = usePreviewModal();

const data = ref([
  {
    date: '2024-08-01',
    type: '식비',
    reason: '야근',
    amount: '100,000원',
    receiptName: '영수증 예시',
    receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
    //receipt: '/img/receipt_example.jpg',
    people: [
      { department: 'SI사업본부', team: '프레임워크', name: '권범준', position: '책임' },
      { department: 'SI사업본부', team: '프레임워크', name: '홍길동', position: '선임' },
    ],
  },
  {
    date: '2024-08-02',
    type: '식비',
    reason: '야근',
    amount: '120,000원',
    receiptName: '영수증 예시',
    receipt: 'https://kwon-beom-jun.github.io/centrally-system-page/img/receipt_example.jpg',
    //receipt: '/img/receipt_example.jpg',
    people: [
      { department: 'SI사업본부', team: '프레임워크', name: '권범준', position: '책임' },
      { department: 'SI사업본부', team: '프레임워크', name: '홍길동', position: '선임' },
      { department: 'SI사업본부', team: '프레임워크', name: '이석순', position: '선임' },
      { department: 'SI사업본부', team: '프레임워크', name: '최순자', position: '선임' },
    ],
  },
  // 추가 데이터...
]);

const formData = ref({
  receiptImage: null,
  date: '',
  type: '식비',
  amount: '',
  reason: '',
  people: []
});

const userSearch = ref('');
const searchResults = ref([]);

// 사용자 검색 함수
const searchUsers = async () => {
  if (userSearch.value.length < 2) {
    searchResults.value = [];
    return;
  }

  // try {
  //   const response = await axios.get('/api/users', {
  //     params: { query: userSearch.value }
  //   });
  //   searchResults.value = response.data;
  // } catch (error) {
  //   console.error('사용자 검색 오류:', error);
  // }

  // ---------------------------------------- 예시 데이터 ----------------------------------------
  // 예시 데이터 사용
  const exampleUsers = [
    { name: '권범준', department: 'SI사업본부', team: '프레임워크', position: '책임' },
    { name: '권범선', department: 'SI사업본부', team: '프레임워크', position: '책임' },
    { name: '권범이', department: 'SI사업본부', team: '프레임워크', position: '책임' },
    { name: '권범삼', department: 'SI사업본부', team: '프레임워크', position: '책임' },
    { name: '홍길동', department: 'SI사업본부', team: '프레임워크', position: '선임' },
    { name: '이영희', department: 'SI사업본부', team: '프레임워크', position: '사원' }
  ];
  // 검색어에 맞는 예시 데이터를 필터링
  searchResults.value = exampleUsers.filter(user =>
    user.name.includes(userSearch.value)
  );
  // --------------------------------------------------------------------------------------------

};

const selectUser = (user) => {
  formData.value.people.push(user);
  userSearch.value = '';
  searchResults.value = [];
};

const selectedItem = ref(null);
const sidebarVisible = ref(false);
const isMobile = ref(window.innerWidth <= 1150);
const currentPage = ref(1);
const itemsPerPage = ref(5);

const closePreviewModalOnOutsideClick = (event) => {
  if (!event.target.classList.contains('preview-modal-image')) {
    isPreviewVisible.value = false;
  }
};

const calculateAmountPerPerson = (item) => {
  const amount = parseInt(item.amount.replace(/[^0-9]/g, ''), 10);
  const peopleCount = item.people.length;
  return peopleCount > 0 ? Math.floor(amount / peopleCount).toLocaleString() + ' 원' : 'N/A';
};

const totalPages = computed(() => Math.ceil(data.value.length / itemsPerPage.value));
const paginatedData = computed(() => {
  const start = (currentPage.value - 1) * itemsPerPage.value;
  return data.value.slice(start, start + itemsPerPage.value);
});

const goToPage = (page) => {
  currentPage.value = page;
};

const modalInstance = ref(null); // 모달 인스턴스를 저장할 ref 추가

const openModal = () => {
  const modal = document.getElementById('receiptModal');
  if (!modalInstance.value) {
    modalInstance.value = new bootstrap.Modal(modal, {
      backdrop: 'static', // 배경 클릭 시 모달이 닫히지 않도록 설정
      keyboard: false, // ESC 키로 모달이 닫히지 않도록 설정
    });
  }
  modalInstance.value.show();
};
const closeModal = () => {
  if (modalInstance.value) {
    modalInstance.value.hide(); // 모달 닫기
  }
};

const handleFileChange = (event) => {
  formData.value.receiptImage = event.target.files[0];
};

const confirmationModalVisible = ref(false);

const submitReceipt = () => {
  confirmationModalVisible.value = true;
};

const saveUserDetails = () => {

  confirmationModalVisible.value = false;
  console.log('Receipt Submitted:', formData.value);

  // 예시로 axios 요청 추가
  // axios.post('/api/submit-receipt', formData.value)
  //   .then(response => {
  //     console.log(response.data);
  //     // 추가 성공 시 알림 등 처리
  //   })
  //   .catch(error => {
  //     console.error('Error submitting receipt:', error);
  //   });

  closeModal();
};


const updateIsMobile = () => {
  isMobile.value = window.innerWidth <= 1150;
};

const openSidebar = (item) => {
  if (!isMobile.value) {
    selectedItem.value = item;
    sidebarVisible.value = true;
  }
};

const closeSidebar = () => {
  sidebarVisible.value = false;
};

const visiblePeopleLists = ref({});

const togglePeopleList = (item) => {
  if (visiblePeopleLists.value[item.date]) {
    delete visiblePeopleLists.value[item.date];
  } else {
    visiblePeopleLists.value[item.date] = true;
  }
};

const showPeopleList = (item) => {
  return visiblePeopleLists.value[item.date];
};

onMounted(() => {
  window.addEventListener('resize', updateIsMobile);
});
</script>

<style scoped>
.content {
  transition: margin-right 0.3s ease;
}

.content-expanded {
  margin-right: 300px;
}

.table {
  width: 100%;
}

.card-layout {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.card {
  width: 100%;
  border: 1px solid #ddd;
  padding: 10px;
  border-radius: 5px;
}

.card-header {
  font-size: 1.2rem;
  font-weight: bold;
}

/* 사이드바 감싸는 wrapper 스타일 및 애니메이션 */
.sidebar-wrapper {
  position: fixed;
  right: 0;
  top: 0;
  width: 300px;
  height: 100%;
  transform: translateX(100%);
  transition: transform 0.3s ease;
  z-index: 1000;
}

/* 사이드바가 보일 때 적용되는 클래스 */
.sidebar-visible {
  transform: translateX(0);
}

.sidebar {
  width: 100%;
  height: 100%;
  background-color: #f9f9f9;
  border-left: 1px solid #ddd;
  padding: 20px;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
}

.status-approved {
  color: green;
  font-weight: bold;
}

.status-pending {
  color: blue;
  font-weight: bold;
}

.status-rejected {
  color: red;
  font-weight: bold;
}

.search-button {
  font-size: 0.875rem;
  padding: 0.4rem 0.75rem;
  margin-bottom: 5px;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  font-size: 1.5rem;
}

.custom-alert-modal {
  /* Bootstrap 모달의 기본 z-index(1050)보다 높게 설정 */
  z-index: 1055;
}

.form-row {
  display: flex;
  gap: 10px;
}

.reason {
  margin-top: 10px;
}

.people-list {
  font-size: 0.7rem;
  max-height: 150px;
  overflow-y: auto;
  margin-top: 10px;
  border-top: 1px solid #ddd;
  padding-top: 10px;
}

.people-list-header {
  font-weight: bold;
  margin-bottom: 5px;
}

.people-list-item {
  padding: 1px 0;
  margin: 0;
  margin-left: 10px;
}

.form-group {
  margin-bottom: 20px;
}

label p {
  margin-bottom: 0;
}

@media (max-width: 768px) {
  .form-group {
    margin-bottom: 10px;
  }

  .form-group .mt-2 {
    margin: 0 !important;
  }

  .form-group label {
    font-size: 0.9rem;
  }

  .form-control-file {
    padding: 0px;
  }

  .card-body {
    padding-bottom: 0;
  }
}
</style>
